<template>
  <div class="information">
    <dl class="info">
      <dt class="left-wrap"><img :src="appDetail.icon"></dt>
      <dd class="middle-wrap">
        <p class="title">{{appDetail.title}}</p>
        <p class="sub-title">{{appDetail.seller}}</p>
      </dd>
    </dl>

    <div class="right">
      <dl>
        <dt>分类</dt>
        <dd>{{appDetail.genres_class_name}}</dd>
      </dl>
      <dl>
        <dt>App ID</dt>
        <dd>{{appDetail.appid}}</dd>
      </dl>
      <dl>
        <dt>价格</dt>
        <dd v-if="appDetail.price==0">免费</dd>
        <dd v-else>{{appDetail.price}}</dd>
      </dl>
      <dl>
        <dt>最新版本</dt>
        <dd>{{appDetail.version}}</dd>
      </dl>
    </div>
  </div>
</template>
<style lang="scss">
  /* information */
  .information {
    position: relative;
    width: 100%;
    .info {
      display: flex;
      align-items: top;
      margin-top: 38px;
      dt.left-wrap {
        width: 92px;
        height: 92px;
        border-radius: 6px;
        border: solid 0.5px rgba(58, 58, 58, 0.2);
        overflow: hidden;

        img {
          width: 92px;
          height: 92px;
      }
    }

      .middle-wrap {
        flex: 1;
        margin-right: 395px;
    }

      p, dd {
        margin: 0;
        padding: 0;
      // overflow: hidden;
      // white-space: nowrap;
      // text-overflow: ellipsis;
    }
      dd {
        margin-left: 20px;
        p.title {
          width: 100%;
          height: 28px;
          line-height: 28px;
          font-family: PingFangSC-Semibold;
          font-size: 20px;
          font-weight: 600;
          color: #3a3a3a;
      }
        p.sub-title {
          margin-top: 10px;
          font-size: 13px;
          line-height: 1.08;
          color: #888888;
      }
    }
  }
    .right {
      position: absolute;
      right: 0;
      top: 0px;
      display: flex;
      align-items: center;
      line-height: 44px;
      font-size: 13px;
      color: #3a3a3a;
      border-top: 1px solid #e8e8e8;
      border-left: 1px solid #e8e8e8;
      dl {
        margin: 0;
        dt {
          padding: 0 18px;
          background-color: #f9f9f9;
          border-right: 1px solid #e8e8e8;
          border-bottom: 1px solid #e8e8e8;
      }
        dd {
          margin: 0;
          padding: 0 18px;
          border-right: 1px solid #e8e8e8;
          border-bottom: 1px solid #e8e8e8;
      }
    }
  }
  }
</style>
<script>
  export default {
    props: {
      appDetail: {
        type: Object,
        default: null
      }
    }
  }
</script>



