import {
  URI_APP_SEARCH,
  URI_APP_ADD,
  URI_APP_DELETE
} from '@/constants'

import { Message } from 'element-ui'
import api from '@/fetch'
const types = {
  ADD_APPLE: 'add_apple',
  DELETE_APPLE: 'delete_apple',
  SYNC_TABLE: 'sync',
  DO_CURRENT_CHANGE: 'do_current_change'
}

const initData = () => {
  return {
    total: 100,
    searchList: [],
    state_currentPage: 100,
    state_keyword: ''
  }
}

const state = initData()

const getters = {}

const mutations = {
  [types.ADD_APPLE] (state, index) {
    state.searchList.splice(index, 1)
  },
  [types.DELETE_APPLE] (state, index) {
    state.searchList.splice(index, 1)
  },
  [types.SYNC_TABLE] (state, params) {
    state.state_keyword = params.keyword
    // state.dataLoading = false
    state.searchList = params.payload.list
    state.total = params.payload.total || 100
  },
  [types.DO_CURRENT_CHANGE] (state, page) {
    state.state_currentPage = page
  }
}

const actions = {
  // 获取分页
  doCurrentChange ({commit}, page) {
    commit(types.DO_CURRENT_CHANGE, page)
  },
  // 添加应用
  addApp ({commit}, params) {
    let uri = `${URI_APP_ADD}?appid=${params.appid}`
    let body = {
      appid: params.appid
    }
    return api((uri), {
      method: 'POST',
      body: body
    }).then((res) => {
      commit(types.ADD_APPLE, params.appid)
    })
  },
  // 删除应用
  deleteApp ({commit}, params = {appid: ''}) {
    let uri = `${URI_APP_DELETE}?appid=${params.appid}`
    let body = {
      appid: params.appid
    }
    return api((uri), {
      method: 'POST',
      body: body
    }).then((res) => {
      Message({
        message: '删除成功',
        iconClass: 'qk-warning'
      })
      commit(types.DELETE_APPLE, params.appid)
    })
  },
  search ({commit, payload}, params = {keyword: ''}) {
    let uri = `${URI_APP_SEARCH}?keyword=${params.keyword}`
    return api(uri, {
      method: 'POST',
      body: params
    })
    .then(res => res && res.payload)
    .then(payload => {
      state.appList = payload.list
      commit(types.SYNC_TABLE, {
        payload: payload,
        keyword: params.keyword
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



