<template>
  <div class="rank rank-container">

    <!-- nav -->
    <div class="nav">
      <div class="title" @click="$router.push('/d/app/list')">我的应用</div>
      <div class="right"><img src="http://qianka.b0.upaiyun.com/images/a688c7dd7a765df07ec7d9cfab76b68f.png"></div>
      <el-select v-model="appid" popper-class="mini" class="small" @change="selectApp">
        <el-option
          v-for="(item, index) in appList"
          :key="index"
          :label="regexTitle(item.title)"
          :value="item.appid">
        </el-option>
      </el-select>
    </div>
    <!-- nav -->

    <!-- information -->
    <app-info :app-detail="myAppDetail"></app-info>
    <!-- information -->

    <!-- tabs -->
    <div>
      <el-tabs v-model="activeName" @tab-click="handleClick" class="qk-tabs-text" type="card">
        <el-tab-pane name="first">
          <span class="qk-tabs__item-text" slot="label"><span class="tag">榜单排名</span></span>
        </el-tab-pane>
        <el-tab-pane name="seco">
          <span class="qk-tabs__item-text" slot="label"><span>ASO关键词</span></span>
        </el-tab-pane>
      </el-tabs>
    </div>
    <!-- tabs -->

    <!-- 图表 -->
    <el-row class="chart-wrapper">
      <div class="chart-nav">
        <el-form class="mrg-l10">
          <div class="block form-search">
            <el-date-picker class="medium" type="daterange"
                            align="right" v-model="dateSelectedRangeArr" placeholder="选择日期范围"
                            range-separator="-"
                            :picker-options="pickerOptions2" format="yyyy.MM.dd" @change="onDateRangeSelected">
            </el-date-picker>
          </div>
        </el-form>
        <div class="chart-title">{{myAppDetail.title}}</div>
      </div>
      <!-- 表格图表 -->
      <div v-show="rankList.length > 0" id="rank-chart"></div>
      <!-- 表格图表 -->
    </el-row>
    <div class="no-data-chart" v-if="rankList && rankList.length === 0">尚未进入当前榜单</div>

  </div>
</template>
<style lang="scss">

  .el-select-dropdown {
    &.mini {
      width: 118px;
    }
  }
  .rank.rank-container {

    width: 100%;
    min-width: 1020px;
    padding: 32px 35px 150px 35px;

    /* nav */
    .nav {
      display: flex;
      align-items: center;
      margin-top: 9px;
      .title {
        cursor: pointer;
        font-size: 16px;
        font-weight: 300;
        color: #888888;
        height: 22px;
        font-family: PingFangSC-Light;
        margin-top: 4px;
      }
      .right {
        margin: 4px 10px 0 10px;
        height: 22px;
      }
      .small {
        width: 118px;
      }
    }

    /* tabs */
    .qk-tabs-text {
      margin-top: 30px;

      .el-tabs__nav {
        height: auto !important;

        .el-tabs__item {
          width: 146px;
          height: 42px !important;
          font-size: 13px;
        }
      }
    }

    .el-pagination {
      margin-top: 40px;
    }

    .chart-wrapper {
      height: 440px;
      margin-bottom: 51px;
      padding-top: 10px;

      #rank-chart{
        z-index: 1;
        width: 100%;
        height:550px;
        margin-top: -35px;
      }

      .chart-nav {
        position: relative;
        z-index: 11;
        display: flex;
        margin-right: 190px;

        .chart-title {
          flex: 1;
          text-align: center;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          font-size: 20px;
          font-weight: 600;
          line-height: 40px;
          color: #3a3a3a;
          padding: 0 20px;
        }
      }
    }
    .change-no-data {
      height: 430px;
      font-family: PingFangSC;
      font-size: 13px;
      font-weight: 500;
      text-align: center;
      color: #b5b5b5;
      line-height: 430px;
      margin-top: -430px;
    }
    .no-data-chart {
      height: 430px;
      font-family: PingFangSC;
      font-size: 13px;
      font-weight: 500;
      text-align: center;
      color: #b5b5b5;
      line-height: 430px;
      margin-top: -430px;
    }
  }
</style>
<script>
  // import _ from 'lodash'
  import {mapState, mapGetters, mapActions} from 'vuex'
  import AppInfo from './App-Info.vue'

  export default {
    components: {
      AppInfo
    },
    data () {
      // 默认显示最近三天排名
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 3)
      return {
        appid: '',
        title: '',
        reportApp: '',    // app名称
        endTime: '',
        startTime: '',
        pickerOptions2: { // 搜索时间的配置
          shortcuts: [{
            text: '近24小时',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 24)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '近三日',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 2)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '近七日',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 6)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '近一月',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 29)
              picker.$emit('pick', [start, end])
            }
          }]
        },
        reportContent: 'cost',
        chart: undefined,
        sort: {prop: 'date', order: 'descending'}, // talble 按时间属性倒序
        activeName: 'first',
        dateSelectedRangeArr: [start, end]
      }
    },

    mounted () {
      this.appid = parseInt(this.$route.params.appid)
      this.doGetMyApp()
      window.addEventListener('resize', this.fetchAppChart)
    },

    destroyed () {
      window.removeEventListener('resize', this.fetchAppChart)
    },

    computed: {
      ...mapState('rank', [
        'appList',
        'rankList',
        'myAppDetail'
      ]),
      ...mapGetters('rank', [
        'chartLabels',
        'chartData'
      ])
    },

    methods: {
      ...mapActions('rank', [
        'doGetMyApp',
        'doGetAppDetail',
        'getChartData',
        'toggleChartData'
      ]),

      // 跳转到榜单排名
      handleClick (tab, event) {
        if (tab.index === '0') {
          return
        } else {
          this.$nextTick(() => {
            this.$router.push('/d/app/aso_keyword/' + this.appid)
          })
        }
      },

      // 选择下拉框
      selectApp (appid) {
        // 设置appid
        this.appid = appid
        // 改变url
        this.$router.push('/d/app/rank/' + appid)
        this.getAppDetail()
        this.fetchAppChart()
      },

      toggleChartLine (data, index) {
        this.toggleChartData({data, index})
        this.drawChart()
      },

      fetchAppChart () {
        let appid = this.$route.params.appid
        let fromTime = parseInt(Date.parse(this.dateSelectedRangeArr[0]) / 1000)
        let toTime = parseInt(Date.parse(this.dateSelectedRangeArr[1]) / 1000)
        this.getChartData({
          appid,
          from_time: fromTime,
          to_time: toTime
        }).then(() => {
          this.drawChart(this.chartLabels, this.chartData)
        })
      },

      drawChart (x, y) {
        // 初始化echarts实例
        let myChart = window.echarts.init(document.getElementById('rank-chart'))
        // 自适应
        myChart.resize()
        // 清空上次的图表数据
        myChart.clear()
        // 设置线条颜色
        let colors = ['#4a90e2', '#6bad5d', '#de4948', '#f7ba2a']

        // 设置图表参数
        let legendData = []
        let series = []
        let maxArr = []
        for (let i of this.rankList) {
          legendData.push(i.genre_name)
          series.push({
            name: i.genre_name,
            data: i.y_data,
            type: 'line',
            symbol: 'circle',
            symbolSize: 4
          })
          maxArr.push(Math.max.apply(Math, i.y_data))
        }

        myChart.setOption({
          color: colors,
          tooltip: {
            trigger: 'axis',
            left: 'right',
            backgroundColor: 'rgba(0, 0, 0, 0.05)',
            textStyle: {
              color: '#888',
              fontSize: 12,
              fontWeight: 600
            },
            padding: 10
          },
          toolbox: {
            right: '19px',
            feature: {
              saveAsImage: {}
            }
          },
          legend: {
            icon: 'rect',
            data: legendData,
            itemWidth: 6,
            itemHeight: 6,
            itemGap: 20,
            bottom: '0'
          },
          grid: {
            left: '5px',
            right: '1px',
            bottom: '40px',
            containLabel: true
          },
          xAxis: {
            type: 'category',
            data: x,
            boundaryGap: false,
            axisLine: {
              show: false,
              lineStyle: {
                color: '#b5b5b5'
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                type: 'solid',
                color: '#e2e2e2'
              }
            },
            axisTick: false
          },
          yAxis: [{
            type: 'value',
            inverse: true,
            min: 1,
            minInterval: 1,
            max: () => {
              return Math.max.apply(Math, maxArr) + 1
            },
            axisLine: {
              lineStyle: {
                opacity: 0,
                color: '#b5b5b5'
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                type: 'solid',
                color: '#e2e2e2'
              }
            },
            axisTick: false
          },
          {
            axisLine: {
              show: true,
              lineStyle: {
                type: 'solid',
                color: '#e2e2e2'
              }
            }
          }],
          series: series
        })
      },

      // 搜索 时间
      onDateRangeSelected () {
        this.fetchAppChart()
      },

      // 获取应用详情
      getAppDetail () {
        this.doGetAppDetail({
          appid: this.appid
        })
      },
      // 格式化标题
      regexTitle (val) {
        val = val.split('·')[0]
        val = val.split('－')[0]
        val = val.split('—')[0]
        return val ? val.split('-')[0] : val
      }
    }
  }
</script>



