<template>
  <div class="container">
    <div class="nav">
      <div class="title">账户首页</div>
    </div>

    <el-row class="cards">
      <!-- 总览 -->
      <el-col :span="6">
        <card>
          <div slot="title">今日消费</div>
          <div slot="highlight">{{ portlet.today_cost | index }}</div>
        </card>
      </el-col>
      <el-col :span="6">
        <card>
          <div slot="title">账户余额</div>
          <div slot="highlight">{{ portlet.balance | index }}</div>
        </card>
      </el-col>
      <el-col :span="6">
        <card>
          <div slot="title">可用金额</div>
          <div slot="highlight">{{ portlet.available_balance | index }}</div>
        </card>
      </el-col>
      <el-col :span="6">
        <card>
          <div slot="title">累计投放额</div>
          <div slot="highlight">{{ portlet.total_cost | index }}</div>
        </card>
      </el-col>
    </el-row>

    <!-- 报表菜单 -->
    <el-row class="report-menus">
      <div class="input-wrapper">
        <el-select class="select-report-app small" popper-class="select-dropdown-home small" v-model="reportApp"
                   placeholder="请选择" @change="onSelect">
          <el-option label="全部应用" value=""></el-option>
          <el-option
            v-for="(rt, index) in app_list"
            :key="index"
            :label="rt.title"
            :value="rt.appid">
          </el-option>
        </el-select>
        <el-form class="mrg-l10" :model="searchForm">
          <div class="block form-search">
            <el-date-picker class="medium" type="daterange"
                            range-separator="-"
                            align="right" v-model="dateWeekTime" placeholder="选择日期范围"
                            :picker-options="pickerOptions2" format="yyyy.MM.dd" @change="onDate">
            </el-date-picker>
          </div>
        </el-form>
      </div>
      <el-button type="primary" v-show="tableData.length > 0" size="medium" @click="download()">下载报表</el-button>
    </el-row>

    <!-- 报表头 Tabs -->
    <el-tabs v-model="reportContent" class="qk-tabs-text" type="card" @tab-click="onTabClick">
      <el-tab-pane name="cost">
        <span class="qk-tabs__item-text" slot="label"><span class="tag">消费</span><span class="sub-tag">{{ reportSummary.cost > 0 ? ('¥ ' + reportSummary.cost.toFixed(2)) : '-' }}</span></span>
      </el-tab-pane>
      <!--<el-tab-pane name="impression">
        <span class="qk-tabs__item-text" slot="label"><span>展示</span><span>{{ reportSummary.impression | index}}</span></span>
      </el-tab-pane>-->
      <el-tab-pane name="clicks">
        <span class="qk-tabs__item-text" slot="label"><span>抢到任务/人</span><span>{{ reportSummary.clicks | index }}</span></span>
      </el-tab-pane>
      <!--<el-tab-pane name="click_rate">
        <span class="qk-tabs__item-text" slot="label"><span>点击率</span><span>{{ reportSummary.click_rate | rate }}</span></span>
      </el-tab-pane>-->
      <el-tab-pane name="effect_actions">
        <span class="qk-tabs__item-text" slot="label"><span>完成任务/人</span><span>{{ reportSummary.effect_actions | index }}</span></span>
      </el-tab-pane>
      <el-tab-pane name="effect_rate">
        <span class="qk-tabs__item-text" slot="label"><span>完成率</span><span>{{ reportSummary.effect_rate | rate }}</span></span>
      </el-tab-pane>
      <el-tab-pane name="zs_finish_total_count">
        <span class="qk-tabs__item-text" slot="label"><span>完成专属/份</span><span>{{ reportSummary.zs_finish_total_count | index }}</span></span>
      </el-tab-pane>
    </el-tabs>

    <!-- 图表 -->
    <el-row class="chart-wrapper">
      <div id="table-chart"></div>
    </el-row>

    <!-- 表格 -->
    <el-row>
      <el-table
        :data="tableData"
        border
        stripe
        :default-sort="sort"
        style="width: 100%;"
      >
        <el-table-column
          prop="date"
          label="日期">
          <template scope="scope">
            <!--<div>{{ scope.row.date.replace(/\//g, '.') }}</div>-->
            <div>{{ scope.row.date }}</div>
          </template>
        </el-table-column>
        <el-table-column
          prop="cost"
          label="消费">
          <template scope="scope">
            <div>{{ '￥' + scope.row.cost | addCommas_money }}</div>
          </template>
        </el-table-column>
        <!--<el-table-column
          prop="impression"
          label="展示">
          <template scope="scope">
            <div>{{ scope.row.impression | addCommas }}</div>
          </template>
        </el-table-column>-->
        <el-table-column
          prop="clicks"
          label="抢到任务/人">
          <template scope="scope">
            <div>{{ scope.row.clicks | addCommas }}</div>
          </template>
        </el-table-column>
        <!--<el-table-column
          prop="click_rate"
          label="点击率">
        </el-table-column>-->
        <el-table-column
          prop="effect_actions"
          label="完成任务/人">
          <template scope="scope">
            <div>{{ scope.row.effect_actions | addCommas }}</div>
          </template>
        </el-table-column>
        <el-table-column
          prop="effect_rate"
          label="完成率">
        </el-table-column>
        <el-table-column
          prop="zs_done_count"
          label="完成专属/份">
        </el-table-column>
      </el-table>
    </el-row>
  </div>
</template>

<style lang='scss'>
  .container {
    width: 100%;
    min-width: 1020px;
    padding: 32px 35px 150px 35px;

    .nav {
      padding: 18px 0;
      height: 58px;

      .title {
        font-family: PingFangSC-Light;
        font-size: 16px;
        line-height: 22px;
        color: #888888;
      }
    }

    .cards {
      padding: 29px 0 10px 0;

      .qk-dash-card {
        padding: 18px 20px;

        .title {
          font-size: 13px;
          line-height: 18px;
          margin-bottom: 8px;
        }
        .highlight {
          margin-bottom: 0;
        }
      }
      .el-col.el-col-6 {
        padding-right: 10px;

        &:last-of-type {
          padding-right: 0px;
        }
      }
    }

    .report-menus {
      padding: 30px 0;
      height: 100px;

      .select-report-app {
        float: left;
        margin-right: 10px;
      }

      .select-report-type {
        margin-right: 10px;
      }

      .select-report-type {
        width: 180px;
      }

    }

    .chart-wrapper {
      height: 440px;
      margin-bottom: 51px;
      padding-top: 10px;

      .rpt-chart {
        width: 100%;
        height: 100%;
      }
      #table-chart {
        width: 100%;
        height: 100%;
      }
    }

    .input-wrapper {
      display: inline-block;
      float: left;
      margin-right: 10px;

      .el-select, .el-form, .el-button {
        display: inline-block;
      }
    }
  }

  .select-dropdown-home {
    .el-select-dropdown__item {
      span {
        color: #3a3a3a;
      }
    }
  }
</style>

<script>
  import _ from 'lodash'
  import {mapState, mapGetters, mapActions} from 'vuex'
  import DashboardCard from '@/components/DashboardCard.vue'
  import {LW, L30D, L90D} from '@/constants'

  export default {
    components: {
      Card: DashboardCard
    },

    filters: {
      index (val) {
        return parseFloat(val) > 0
          ? ('¥ ' + parseFloat(val).toLocaleString())
          : '-'
      },

      rate (val) {
        return val === '0.00%'
          ? '-'
          : val
      },

      addCommas: function (value) {
        value += ''
        const x = value.split('.')
        var x1 = x[0]
        const x2 = x.length > 1 ? '.' + x[1] : ''
        const rgx = /(\d+)(\d{3})/
        if (rgx.test(x1)) {
          x1 = x1.replace(rgx, '$1' + ',' + '$2')
        }
        return x1 + x2
      },

      addCommas_money: function (value) {
        value += ''
        const x = value.split('.')
        var x1 = x[0]
        const x2 = x.length > 1 ? '.' + x[1] : ''
        const rgx = /(\d+)(\d{3})/
        if (rgx.test(x1)) {
          x1 = x1.replace(rgx, '$1' + ',' + '$2')
        }
        var ret = x1 + x2
//        if (ret.indexOf('.') === -1) {
//          ret = ret + '.00'
//        }
        return ret
      }
    },

    data () {
      return {
        value1: '',
        reportApp: '',    // app名称
        reportType: LW,
        dateWeekTime: '', // 搜索时间
        endTime: '',
        startTime: '',
        pickerOptions2: { // 搜索时间的配置
          shortcuts: [{
            text: '最近一周',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '最近一个月',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '最近三个月',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            }
          }]
        },
        reportContent: 'cost',
        reportContentName: '消费',
        chart: undefined,
        sort: {prop: 'date', order: 'descending'} // talble 按时间属性倒序
      }
    },

    fetchAction: 'user/getDashboardData',

    computed: {
      ...mapGetters('user', [
//        'indicesLastday',
//        'indicesDBY',
        'reportApps',
        'reportTypes',
        'reportSummary',
        'tableData',
        'chartLabels',
        'chartData'
      ]),

      ...mapState('user', [
        'searchForm',
        'app_list',
        'navbar',
        'portlet'
      ]),

      'dayCnt': function () {
        switch (this.reportType) {
          case LW:
            return 7
          case L30D:
            return 30
          case L90D:
            return 90
        }
      }
    },

    created () {
      // 设置默认日期
      this.endTime = parseInt((new Date()).getTime() / 1000)
      this.startTime = this.endTime - 86400 * 7

      // 表格数据
      this.getTableData({
        content: this.reportContent,
        appId: this.reportApp,
        endTime: this.endTime,
        startTime: this.startTime
      })

      window.addEventListener('resize', this.drawChart)
    },

    mounted () {
      this.drawChart()
    },

    destroyed () {
      window.removeEventListener('resize', this.drawChart)
    },

    methods: {
      onTabClick (tab, event) {
        let reportNameArr = ['消费', '抢到任务', '完成任务', '完成率', '完成专属']
        this.reportContentName = reportNameArr[tab.index]
        this.getTableData({
          content: tab.name,
          appId: this.reportApp,
          endTime: this.endTime,
          startTime: this.startTime
        })

        this.drawChart()
      },

      onSelect () {
        this.getTableData({
          content: this.reportContent,
          appId: this.reportApp,
          endTime: this.endTime,
          startTime: this.startTime
        })
        this.drawChart()
      },

      drawChart () {
        this.getChartData({
          content: this.reportContent,
          appId: this.reportApp,
          endTime: this.endTime,
          startTime: this.startTime
        }).then(() => {
          let myChart = window.echarts.init(document.getElementById('table-chart'))
          // 自适应
          myChart.resize()
          // 清空上次的图表数据
          myChart.clear()
          let data = []
          let labels = this.chartLabels
          let max = 0
          _.each(this.chartData, (v, i) => {
            data.push([i, v])
            if (v > max) max = v
          })
          max = max > 0 ? max * 1.5 : 101

          // 矫正x轴
          switch (data.length) {
            case 7:
              data.push([data.length - 1 + 0.2, null])
              data.splice(0, 0, [-0.2, null])
              break
            case 30:
              data.push([data.length - 1 + 2, null])
              data.splice(0, 0, [-2, null])
              break
            case 90:
              data.push([data.length - 1 + 3, null])
              data.splice(0, 0, [-3, null])
              break
          }

          // x轴 ticks
          const makeTicks = () => {
            let len = data.length - 2
            let n = len % 10 === 0 ? len / 10 : 1
            let ticks = []
            _.each(labels, (l, i) => {
              if (i % n === 0 || i === labels.length - 1) ticks.push([l])
            })
            return ticks
          }
          let options = {
            color: ['#4a90e2'],
            tooltip: {
              trigger: 'axis',
              left: 'right',
              backgroundColor: 'rgba(0, 0, 0, 0.05)',
              textStyle: {
                color: '#888',
                fontSize: 13,
                fontWeight: '600'
              },
              padding: 12
            },
            toolbox: {
              right: '19px',
              feature: {
                saveAsImage: {}
              }
            },
            grid: {
              left: '15px',
              right: '1px',
              bottom: '30px',
              containLabel: true,
              borderColor: '#e2e2e2',
              borderWidth: 1,
              tooltip: {
                axisPointer: {
                  type: 'line',
                  lineStyle: {
                    color: '#b5b5b5'
                  }
                }
              }
            },
            xAxis: {
              type: 'category',
              data: makeTicks(),
              boundaryGap: false,
              axisLine: {
                show: false,
                lineStyle: {
                  color: '#b5b5b5'
                }
              },
              splitLine: {
                show: true,
                lineStyle: {
                  type: 'solid',
                  color: '#e2e2e2'
                }
              },
              axisTick: false
            },
            yAxis: [{
              type: 'value',
              min: 0,
              minInterval: 1,
              max: () => {
                return Math.max.apply(Math, this.chartData) + 1
              },
              axisLine: {
                lineStyle: {
                  opacity: 0,
                  color: '#b5b5b5'
                }
              },
              splitLine: {
                show: true,
                lineStyle: {
                  type: 'solid',
                  color: '#e2e2e2'
                }
              },
              axisTick: false
            },
            {
              axisLine: {
                show: true,
                lineStyle: {
                  type: 'solid',
                  color: '#e2e2e2'
                }
              }
            }],
            series: [{
              name: this.reportContentName,
              data: this.chartData,
              type: 'line',
              symbol: 'circle',
              symbolSize: 4
            }]
          }
          myChart.setOption(options)
        })
      },

      onTimeChange () {
        this.getTableDataByTime({
          content: this.reportContent,
          appId: this.reportApp,
          endTime: this.endTime,
          startTime: this.startTime
        })

        this.drawChart()
      },

      // 搜索 时间
      onDate () {
        this.startTime = Date.parse(new Date(this.dateWeekTime[0])) / 1000 ? Date.parse(new Date(this.dateWeekTime[0])) / 1000 : this.startTime
        this.endTime = Date.parse(new Date(this.dateWeekTime[1])) / 1000 ? Date.parse(new Date(this.dateWeekTime[1])) / 1000 : this.endTime
        this.onTimeChange()
      },

      download () {
        this.$store.dispatch('user/downloadReport', {appId: this.reportApp, endTime: this.endTime, startTime: this.startTime})
      },

      ...mapActions('user', [
        'getTableData',
        'getChartData',
        'getTableDataByTime'
      ])
    }
  }
</script>



