<script>
  import util from '@/utils'
  import { mapState, mapActions } from 'vuex'
  import BindPhoneDialog from '@/components/BindPhoneDialog.vue'
  import ConfirmDelDialog from '@/components/ConfirmDelDialog'

  export default {
    components: {
      BindPhoneDialog,
      ConfirmDelDialog
    },

    data () {
      return {
        isRequestEnd: false,
        loading: true, // loading 显示开关
        activeName: 'tab1',   // 选中的tab的 名字
        app_name: '',
        currentPage: 1, // 当前在第几页 for pagination
        dateWeekTime: [null, null], // startTime, endTime
        currentStatus: 'ok', // 当前路由的参数
        pickerOptions2: { // 搜索时间的配置
          shortcuts: [{
            text: '近七日',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '近一月',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '近三月',
            onClick (picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            }
          }]
        },
        dialogContinueTotalVisible: false, // 续总数弹层 显示开关
        currentTaskId: '', // 当前选中的任务ID
        taskName: '', // 续总数标题
        continueTotalForm: { // 续总数表单
          add_number: ''
        },
        dialogPreviewVisible: false, // 预览弹层 显示开关
        deleting: null, // 要删除的任务信息 json
        minWidth: 10
      }
    },

    filters: {
      limitLen: function (val, len = 8) {
        let newVal = val
        newVal = newVal.split('-')[0]
        if (newVal.trim().length > len) {
          newVal = newVal.substr(0, 8) + '...'
        }
        return newVal
      },
      formatZero: function (val) {
        if (val === 0) {
          return '-'
        } else {
          return val
        }
      },

      addCommas: function (value) {
        value += ''
        const x = value.split('.')
        var x1 = x[0]
        const x2 = x.length > 1 ? '.' + x[1] : ''
        const rgx = /(\d+)(\d{3})/
        if (rgx.test(x1)) {
          x1 = x1.replace(rgx, '$1' + ',' + '$2')
        }
        return x1 + x2
      },
      addCommas_money: function (value) {
        value += ''
        const x = value.split('.')
        var x1 = x[0]
        const x2 = x.length > 1 ? '.' + x[1] : ''
        const rgx = /(\d+)(\d{3})/
        if (rgx.test(x1)) {
          x1 = x1.replace(rgx, '$1' + ',' + '$2')
        }
        var ret = x1 + x2
//        if (ret.indexOf('.') === -1) {
//          ret = ret + '.00'
//        }
        return ret
      }
    },

    computed: {
      ...mapState('ad', [
        'task_statcnt',
        'totalTasks',
        'tableData',
        'searchForm',
        'previewForm',
        'ad_price',
        'newProductActivity',
        'appList'
      ])
    },

    mounted () {
      var type = this.task_status = this.$route.params.status.split('&')[0]

      var activeName = 'tab1'
      switch (type) {
        case 'ok':
          activeName = 'tab1'
          break
        case 'pending':
          activeName = 'tab2'
          break
        case 'rejected':
          activeName = 'tab3'
          break
        case 'paused':
          activeName = 'tab4'
          break
        case 'ended':
          activeName = 'tab5'
          break
      }
      this.activeName = activeName
      // 获取广告 table list
      this.getAdvertisement()
    },

    methods: {
      ...mapActions('ad', [
        'searchAdTask',
        'taskToEnd',
        'addTaskNumber',
        'previewTask',
        'deleTask',
        'exportIDFA',
        'resume'
      ]),
      getAdvertisement () {
        this.dateWeekTime = []
        this.searchTask()
      },
      // 搜索 时间、标题
      searchTask () {
        this.currentPage = 1
        let currentStatus = this.currentStatus = this.$route.params.status.split('&')[0]
        let url = '/v2/api/task?status=' + currentStatus + '&page_index=' + this.currentPage
        // 按时间搜索
        let startTime = this.dateWeekTime[0]
        let endTime = this.dateWeekTime[1]
        if (this.dateWeekTime.length >= 2 && startTime) {
          // 结束时间取值到 23:59:59
          let end = util.formatTime(endTime.getTime() / 1000)
          end = end.split(' ')[0] + ' 23:59:59'
          url += '&kw_begin=' + util.formatTime(startTime.getTime() / 1000) + '&kw_end=' + end
        }
        // 按标题搜索
        if (this.app_name) {
          url += '&app_name=' + this.app_name
        }
        this.loading = true
        this.searchAdTask({
          url: url,
          status: currentStatus
        }).then(() => {
          this.loading = false
          this.isRequestEnd = true
        }).catch(() => {
          this.loading = false
        })
      },
      // 前往 添加广告
      toAddAd () {
        this.$router.push('/d/ad/new')
      },
      handleClick (key) {
        if (this.$route.params.status === key) {
          return
        }
        let routePath = '/d/ad/ios'
        var name = key.name
        // tab1 tab2 tab3 tab4 tab5
        switch (name) {
          case 'tab1':
            routePath += '/ok'
            this.$route.params.status = 'ok'
            this.$router.push(routePath)
            break
          case 'tab2':
            routePath += '/pending'
            this.$route.params.status = 'pending'
            this.$router.push(routePath)
            break
          case 'tab3':
            routePath += '/rejected'
            this.$route.params.status = 'rejected'
            this.$router.push(routePath)
            break
          case 'tab4':
            routePath += '/paused'
            this.$route.params.status = 'paused'
            this.$router.push(routePath)
            break
          case 'tab5':
            routePath += '/ended'
            this.$route.params.status = 'ended'
            this.$router.push(routePath)
            break
        }

        this.currentPage = 1
        this.activeName = name
        this.searchTask()

        this.$router.push(routePath)
      },
      handleCurrentChange (val) {
        this.currentPage = val
        this.searchTask()
      },
      // 手动 完成
      goToEnded (row) {
        this.taskToEnd(row.id).then(data => {
          if (data && data.status === 'ok') {
            this.$route.params.status = 'ended'
            this.activeName = 'tab5' // 要留在vue文件中
            this.searchTask()
          }
        })
      },

       // 续总数
      addTotalNumber () {
        let config = {
          currentTaskId: this.currentTaskId,
          addNumber: this.continueTotalForm.add_number
        }
        this.addTaskNumber(config)
        .catch(e => {
          this.$message({
            message: e.err_msg,
            iconClass: 'qk-warning'
          })
        })
      },
      // 显示 续总数弹窗
      addNumber (row) {
        this.taskName = row.task
        this.dialogContinueTotalVisible = true
        this.currentTaskId = row.id
      },
      // 编辑
      editTask (row) {
        this.$router.push({ name: 'dash_ad_edit', params: { taskId: row.id } })
      },
      // 预览
      previewTaskInfo (row) {
        this.previewTask(row.id).then(() => {
          this.dialogPreviewVisible = true
        })
      },
      showDialogPreview () {
        /* ?todo? 检查是否冗余 */
        if (this.dialogPreviewVisible) {
          this.dialogPreviewVisible = false
        } else {
          this.dialogPreviewVisible = true
        }
      },
      // 删除
      removeTask (index, row) {
        this.deleting = { index: index, id: row.id }
        this.$refs.confirmDelDialog.show()
      },
      handleDelete () {
        this.deleTask(this.deleting).then(_ => {
          this.$refs.confirmDelDialog.hide()
        })
      },
      // 完成状态时 续单
      readd (row) {
        this.$router.push({ name: 'dash_ad_renew', params: { taskId: row.id } })
      },
      // 提取idfa
      exportIdfa (type, row) {
        let config = {
          type: type,
          id: row.id
        }
        this.exportIDFA(config).then(_ => {
          this.searchTask()
        })
      },
      // 下载idfa
      downloadIdfa (type, row) {
        window.location.href = '//' + window.location.hostname + ':' + window.location.port + '/v2/api/task/' + row.id + '/' + type + '/statistics/download'
      },
      // 开启
      resumeTask (row) {
        this.resume(row.id).then(data => {
          if (data && data.status === 'ok') {
            this.$route.params.status = 'ok'
            this.activeName = 'tab1' // 要留在vue文件中
            this.getAdvertisement()
          }
        })
      },

      showTelDialog () {
        let telDialog = this.$refs.bindPhoneDialog
        telDialog.show()
      },

      refreshPhoneAfterBindSuccess (mobile) {
        this.newProductActivity.mobile = mobile
      }
    }
  }
</script>



