import { URI_APP_APPLIST, URI_APP_DELETE } from '@/constants'
import api from '@/fetch'
import { Message } from 'element-ui'
const types = {
  SYNC_TABLE: 'sync',
  DELETE_APP: 'delete_app'
}

const initData = () => {
  return {
    tableData: [],
    totalApps: 0
  }
}

const state = initData()

const getters = {}

const mutations = {
  [types.SYNC_TABLE] (state, payload) {
    state.tableData = payload.list
    state.totalApps = payload.total || 100
  },
  [types.DELETE_APP] (state, index) {
    state.tableData.splice(index, 1)
  }
}

const actions = {
  delApp ({commit}, params = {appid: '', index: ''}) {
    console.log(params)
    return api(URI_APP_DELETE, {
      method: 'POST',
      body: {appid: params.appid}
    }).then(res => {
      commit(types.DELETE_APP, params.index)
      Message({
        iconClass: 'qk-warning',
        message: res.payload.message
      })
    })
  },
  applist ({commit, dispatch}, params = {keyword: '', page: 1}) {
    let uri = `${URI_APP_APPLIST}?keyword=${params.keyword}&page=${params.page}`
    return api(uri).then(({payload}) => {
      if (payload.navbar) dispatch('updateNavbar', payload, {root: true})
      commit(types.SYNC_TABLE, payload)
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



