<template>
  <!--删除-->
  <el-dialog title="删除" custom-class="deleDialog qk-dialog-warning" :show-close="false" v-model="visible" style="top: 30%;">
    <img class="logo-waring" src="//qianka.b0.upaiyun.com/images/833ad156825ac0811aa84f2c29f6f94e.png" alt="">
    <span class="qk-title">此操作将删除该信息，是否继续？</span>
    <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="visible = false" is-plaint>否</el-button>
        <el-button size="small" type="primary" @click="handleDelete()">是</el-button>
      </span>
  </el-dialog>
</template>
<style lang="scss">
  .deleDialog {
    width: 390px;

    .el-dialog__body {
      padding-left: 45px;
      padding-top: 35px;
      position: relative;

      .logo-waring {
        position: absolute;
        left: 0px;
        top: 30px;
      }

      .qk-title {
        color: #3A3A3A;
        font-family: PingFangSC-Regular;
        font-size: 14px;
        line-height: 21px;
      }
    }
    .el-dialog__footer {
      padding-top: 13px;

      .dialog-footer {
        font-size: 0px;

        .el-button {
          width: 70px;
        }

        .el-button:last-child {
          margin-left: 8px;
        }
      }
    }
  }
</style>
<script>
  export default {
    props: {
      handleDelete: {
        type: Function
      }
    },
    data () {
      return {
        visible: false
      }
    },
    methods: {
      show () {
        this.visible = true
      },
      hide () {
        this.visible = false
      }
    }
  }
</script>



