  .el-breadcrumb__item {
    font-size: 16px;
    color: #888888;
    letter-spacing: 0;
  }

  .add-keyword-item {
    display: inline-block;
    margin-right: 10px;
    cursor: pointer;
    width: 190px;
    height: 40px;
