</style>
<script>
  import util from '@/utils'
  import _ from 'lodash'
  import { mapState, mapActions } from 'vuex'
  import PhoneBindDialog from '@/components/BindPhoneDialog.vue'
  export default {
    components: {
      PhoneBindDialog
    },

    data () {
      return {
        appid: '', //  备份appid，为编辑时未添加应用，下拉显示为空
        appSelectChangeNum: 0, // app应用切换次数
        appAddDialogVisible: false, // 续单应用添加弹层
        balanceDialogVisible: false, // 余额不足弹层
        isRequestEnd: false, // 添加新广告，预加载请求是否结束
        currentPage: 1,
        hashKeywordSelection: {}, // 选词助手缓存每页选择的数据
        recommKeywordPageList: [], // 前端分页使用
        keywordPageSize: 7, // 一页7条数据
        multipleKeywordSelection: [],
        keywordDialogVisible: false, // 选词助手
        keywordListLoading: false, // 选词助手table loading
        showAppJumpUrlInput: false, // 跳转链接
        renewLockInput: false, // app应用是否可编辑
        promotionDialogVisible: false, // 新品优惠
        page_sub_title: '添加新广告',  // ？todo 检查是否冗余？
        submitButtonDisable: false,  // 提交按钮是否禁用
        fullscreenLoading: false, // loading是否显示
        cancelDialogVisible: false, // 取消弹层 是否显示
        priceChangeDialogVisable: false, // 价格发生变动时的提示弹层
        showClose: false, // 弹层close按钮不显示
        submitNum: 0 // 防止多次提交
      }
    },

    watch: {
      'adForm.plan_count': function (v) {
        if (v >= 5000) {
          this.changeZsNum(1)
        } else {
          this.changeZsNum(2)
        }
      }
    },

    computed: {
      ...mapState('adNew', [
        'adForm',
        'ad_price',
        'sum_cost',
        'available_balance',
        'advertiser_id',
        'recommKeywordList',
        'appList'
      ]),
      rules () {
        let appIdCheck = (rule, value, callback) => {
          let message
          if (value === '') {
            message = 'App ID 不能为空'
          } else if (/[^\d]/g.test(value)) {
            message = 'App ID 只能为数字'
          }
          if (message) {
            this.$message({
              message: message,
              iconClss: 'qk-warning'
            })
            callback(new Error(' '))
          } else {
            callback()
          }
        }
        let countConsume = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('计划份数不能为空'))
          } else if (!(value > 0)) {
            callback(new Error('计划份数不能少于0且只能为数字'))
          } else {
            callback()
          }
        }

        let limitTitleLen = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('应用标题不能为空'))
          } else if (value.length > 50) {
            callback(new Error('应用标题不能超过50字符'))
          } else {
            callback()
          }
        }
        return {
          title: [{validator: limitTitleLen, tigger: 'blur'}],
          appid: [{validator: appIdCheck, trigger: 'blur'}],
          plan_count: [{validator: countConsume, trigger: 'blur'}],
          begin_time: [{type: 'date', required: true, message: '请选择日期', trigger: 'blur'}],
          begin_time_time: [{type: 'date', required: true, message: '请选择时间', trigger: 'blur'}],
          end_time: [{type: 'date', required: true, message: '请选择日期', trigger: 'blur'}],
          end_time_time: [{type: 'date', required: true, message: '请选择时间', trigger: 'blur'}]
        }
      },
      oldPrice () {
        let oldPrice = this.adForm.appstore_type === 1 ? this.ad_price.unit_price_level2 : this.ad_price.unit_price
        return oldPrice
      },
      newPrice () {
        let newPrice = this.adForm.appstore_type === 1 ? this.ad_price.unit_price : this.ad_price.unit_price_level2
        return newPrice
      }
    },

    mounted () {
      let path = this.$route.name
      let taskId = this.$route.params.taskId
      let appid = this.$route.params.appid
      let backOnAppNotFound = (res) => {
        if (res && res.notFound) {
          this.$router.push('/d/ad/ios/ok')
        }
      }
      if (path === 'dash_ad_renew') {
        // 续单
        this.page_sub_title = '续单'
        this.fetchPreRenew(taskId).then(res => {
          this.isRequestEnd = true
          if (res.price_change) {
            this.priceChangeDialogVisable = true
          }
          if (!res.is_add) {
            this.appAddDialogVisible = true
          }
          backOnAppNotFound(res)
        })
        // 应用是否可编辑
        this.renewLockInput = true
      } else if (/dash_ad_new/.test(path)) {
        // 添加
        this.page_sub_title = '添加新广告'
        this.fetchPreNew({appid, fromPathName: this.$route.query.from}).then(() => {
          this.isRequestEnd = true
          // 默认选中第一个
          if (this.appList.length > 0) {
            this.adForm.appid = this.appList[0].appid
          }
          // 根据appid参数选中应用
          if (this.$route.params.appid) {
            this.adForm.appid = parseInt(this.$route.params.appid)
          }
        })
      } else {
        // 编辑
        this.page_sub_title = '编辑'
        this.fetchPreEdit(taskId).then(res => {
          // 请求完成
          this.isRequestEnd = true
          this.adForm.appid = parseInt(this.adForm.appid)
          if (!res.is_add) {
            // 做个备份，后续添加到我的应用时使用
            this.appid = this.adForm.appid
            this.adForm.appid = ''
            this.resetFormByAppSelect()
            this.appAddDialogVisible = true
          }
          backOnAppNotFound(res)
        })
      }
      // 回到顶部
      window.scrollTo(0, 0)
    },

    destroyed () {
      this.resetForm()
      this.appSelectChangeNum = 0
    },

    filters: {
      currency (val) {
        return val.toLocaleString(
          'ja-JP',
          {
            style: 'currency',
            currency: 'JPY'
          })
      }
    },

    methods: {
      ...mapActions('adNew', [
        'resetForm',
        'resetFormByAppSelect',
        'fetchPreNew',
        'fetchPreRenew',
        'fetchPreEdit',
        'postForm',
        'planAnalysis',
        'rePlanAnalysis',
        'addKeyWordList',
        'removeKeyWordsItem',
        'addZSLists',
        'removeZSItem',
        'checkAppId',
        'countSumCost',
        'changeAppstoreType',
        'changeZsNum',
        'getKeywordList',
        'getKeywordRank',
        'addKeywordsToPlanList'
      ]),

      ...mapActions('add', [
        'addApp'
      ]),

      temporaryNotAddToAppList () {
        this.appAddDialogVisible = false
        this.adForm.appid = ''
        this.adForm.title = ''
        this.resetFormByAppSelect()
      },

      addToAppList () {
        // 续单、编辑时，未添加应用提示添加，为空时，取备份appid
        this.addApp({
          appid: this.adForm.appid || this.appid
        }).then(() => {
          this.appAddDialogVisible = false
          this.$message({
            message: '添加应用成功',
            iconClass: 'qk-warning'
          })
          setTimeout(() => {
            // 编辑的时候刷新，为了下拉应用选中该应用
            if (this.$route.name === 'dash_ad_edit') {
              location.reload()
            }
          }, 1000)
        })
      },

      noRecharge () {
        this.balanceDialogVisible = false
      },

      goRecharge () {
        this.balanceDialogVisible = false
        this.$router.push('/d/finance/charge')
      },

      forwardTo (path) {
        this.$router.push(path)
      },

      appSelectChange (appid) {
        // 过滤无效appid
        if (!appid) {
          return
        }
        // 选择应用时清空之前未保存数据
        this.appSelectChangeNum ++
        if (this.appSelectChangeNum > 1) {
          this.resetFormByAppSelect()
        }
        this.appList.forEach((item, index) => {
          // 根据选择的应用appid给title赋值
          if (item.appid === appid) {
            this.adForm.title = item.title
            return
          }
        })
        this.checkAppId(appid)
      },

      showKeywordDialog () {
        // 最近投放按钮：显示选词助手dialog
        this.keywordDialogVisible = true
        this.keywordListLoading = true
        this.getKeywordList().then(() => {
          this.keywordListLoading = false
          this.recommKeywordPageList = this.recommKeywordList.slice(0, 7)
        }, () => {
          this.keywordListLoading = false
        })
      },

      handleKeywordSelectionChange (val) {
        // checkbox选择事件
        if (val.length > 0) {
          this.hashKeywordSelection[this.currentPage] = val
        }
      },

      closeKeywordDialog () {
        // 确定按钮
        for (let item in this.hashKeywordSelection) {
          this.multipleKeywordSelection.push(...this.hashKeywordSelection[item])
        }
        console.log(this.multipleKeywordSelection)
        this.addKeywordsToPlanList(this.multipleKeywordSelection)
        this.keywordDialogVisible = false
      },

      resetKeywordDialog () {
        // 关闭选词助手dialog回调，状态重置
        this.currentPage = 1
        // 保存每页的checkbox的选中状态
        this.hashKeywordSelection = {}
        // 保存所有的选中关键词
        this.multipleKeywordSelection = []
        this.$refs.multipleKeywordTable.clearSelection()
      },

      toggleSelection (rows) {
        // 切换选中状态
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleKeywordTable.toggleRowSelection(row)
          })
        } else {
          this.$refs.multipleKeywordTable.clearSelection()
        }
      },
      handleKeywordsPageChange (page) {
        // 前端分页、选中状态切换
        this.currentPage = page
        let next = page * this.keywordPageSize
        this.recommKeywordPageList = this.recommKeywordList.slice(next - this.keywordPageSize, next)
        this.$nextTick(() => {
          this.toggleSelection(this.hashKeywordSelection[page])
        })
      },

      // 回IOS广告页
      toIOS () {
        this.$router.push('/d/ad/ios/ok')
      },
      // 续单、编辑、添加 的验证+提交
      submitForm (formName) {
        if (!this.adForm.isHasBindMobile && this.adForm.isNew && this.adForm.appstore_type !== 1) {
          this.promotionDialogVisible = true
          return
        }
        this.$refs[formName].validate((valid) => {
          if (this.submitNum > 0) return
          this.submitNum = 1
          if (valid) {
            if (this.validForm()) {
              // 是否余额不足
              if (this.sum_cost > this.available_balance) {
                this.balanceDialogVisible = true
                this.submitNum = 0
                return
              }
              this.submitButtonDisable = true
              this.fullscreenLoading = true
              this.postFormData()
            } else {
              this.submitNum = 0
            }
          } else {
            this.submitNum = 0
            return false
          }
        })
      },

      // 续单、编辑、添加 的验证
      validForm () {
        let beginDate = util.formatTime(this.adForm.begin_time.getTime() / 1000).substr(0, 10)
        let beginTime = util.formatTime(this.adForm.begin_time_time.getTime() / 1000, 'HH:mm').substr(11)
        let endDate = util.formatTime(this.adForm.end_time.getTime() / 1000).substr(0, 10)
        let endTime = util.formatTime(this.adForm.end_time_time.getTime() / 1000, 'HH:mm').substr(11)

        let bt = beginDate + ' ' + beginTime
        let et = endDate + ' ' + endTime

        let hideLoadingAndEnableBtn = () => {
          this.submitButtonDisable = false
          this.fullscreenLoading = false
        }
        let popMessage = (message) => {
          this.$message({
            message: message,
            iconClass: 'qk-warning'
          })
          hideLoadingAndEnableBtn()
        }

        if (new Date(bt.replace(/-/g, '/')).getTime() < new Date().getTime()) {
          popMessage('开始时间不得小于当前时间')
          return false
        } else if (new Date(bt.replace(/-/g, '/')).getTime() > new Date(et).getTime()) {
          popMessage('结束时间不得小于开始时间')
          return false
        }

        if (this.adForm.planlist.length > 0) {
          let currentCount = 0
          let isPass = true
          let isGTZero = true
          let rankGreater150 = false
          _.each(this.adForm.planlist, function (p) {
            let num = parseInt(p.num)
            currentCount += num
            if (num <= 0) {
              isGTZero = false
              return
            }
            if (num < 100) {
              isPass = false
              return
            }
            // 当含有排名在150名以外的关键词，无法提交
            if (p.rank > 150 || p.rank === 0) {
              rankGreater150 = true
              return
            }
          })
          let advId = this.advertiser_id
          let whiteList = [45]
          if (whiteList.indexOf(advId) > -1) {
            if (!isGTZero) {
              popMessage('单个关键词的计划份数必须大于0份')
              return false
            }
            return true
          }
          if (rankGreater150) {
            popMessage('含有排名在150名以外或者为0的关键词，无法提交')
            return false
          }
          if (!isPass) {
            popMessage('单个关键词的计划份数最少为100份')
            return false
          }
          if (currentCount !== parseInt(this.adForm.plan_count)) {
            popMessage('关键词分配份数之和需等于总计划份数')
            return false
          }
        }
        return true
      },

      // 续单、编辑、添加 的提交
      postFormData () {
        this.submitNum = 1
        var tempForm = util.clone(this.adForm)
        var beginTime = tempForm.begin_time.toString()
        var endTime = tempForm.end_time.toString()
        if (_.includes(beginTime, 'GMT')) {
          tempForm.begin_time = util.formatTime(tempForm.begin_time.getTime() / 1000)
        }
        if (_.includes(endTime, 'GMT')) {
          tempForm.end_time = util.formatTime(tempForm.end_time.getTime() / 1000)
        }
        // 续单时，未知原因，时分秒会被去掉，导致结束时间校验失败， 这里手动检测并加上
        if (beginTime.length < 11) {
          tempForm.begin_time = tempForm.begin_time + ' 00:00:00'
        }
        if (endTime.length < 11) {
          tempForm.end_time = tempForm.end_time + ' 00:00:00'
        }

        tempForm.planlist.length > 0 ? tempForm.kw_flag_needed = 1 : tempForm.kw_flag_needed = 0
        if (tempForm.planlist.length > 0) {
          _.forEach(tempForm.planlist, function (p, index) {
            var temkey = 'planlist-' + index + '-key'
            var temnum = 'planlist-' + index + '-num'
            tempForm[temkey] = p.key
            tempForm[temnum] = p.num
          })
        }
        tempForm.zs_task.length > 0 ? tempForm.zs_task_needed = 1 : tempForm.zs_task_needed = 0
        tempForm.platform === '仅 iPhone 显示' ? tempForm.platform = '1'
          : (tempForm.platform === '仅 iPad 显示' ? tempForm.platform = '2'
          : tempForm.platform = '3')

        tempForm.begin_time_time = util.formatTime(this.adForm.begin_time_time.getTime() / 1000, 'HH:mm').substr(11)
        tempForm.end_time_time = util.formatTime(this.adForm.end_time_time.getTime() / 1000, 'HH:mm').substr(11)

        var postData = tempForm

        let params = ''
        this.$route.name === 'dash_ad_edit' ? params = this.$route.params.taskId : (this.$route.name === 'dash_ad_renew' ? params = this.$route.params.taskId : params = '')

        let config = {
          params: params,
          postData: postData,
          routeName: this.$route.name
        }
        this.postForm(config)
          .then(res => {
            // console.log('post', res)
            this.submitNum = 0
            if (this.$route.name === 'dash_ad_renew') {
              this.$message({
                message: '续单成功！',
                iconClass: 'qk-warning'
              })
            }
            //  ？TODO ？ 什么作用?
            postData.platform === 1 ? postData.platform = '仅 iPhone 显示'
              : (postData.platform === 2 ? postData.platform = '仅 iPad 显示'
              : postData.platform = '两者都是')
            postData.appstore_type === 0 ? postData.appstore_type = '免费' : postData.appstore_type = '付费'
            postData.appstore_cost = this.adForm.appstore_cost
            setTimeout(() => {
              this.submitButtonDisable = false
              this.fullscreenLoading = false
              setTimeout(() => {
                this.$router.push('/d/ad/ios/ok')
              })
            }, 500)
          })
          .catch((e) => {
            this.submitNum = 0
            this.submitButtonDisable = false
            this.fullscreenLoading = false
            this.$message({
              message: e.err_msg,
              iconClass: 'qk-warning'
            })
          })
      },

      // 添加关键词
      addKeyWords () {
        this.addKeyWordList()
      },

      // 删除关键词
      removeKeyWords (keyTime) {
        this.removeKeyWordsItem(keyTime)
        this.planAnalysis()
      },

      // 添加专属任务
      addZS () {
        this.addZSLists()
      },

      // 删除专属任务
      removeZS (key) {
        this.removeZSItem(key)
      },

      toAd () {
        this.$router.push('/d/ad/ios/ok')
      },

      bindTel () {
        this.promotionDialogVisible = false
        let phoneDialog = this.$refs.bindPhoneDialog
        phoneDialog.show()
      },

      bindPhoneSuccess () {
        this.adForm.isHasBindMobile = true
      },

      continueSubmit () {
        this.promotionDialogVisible = false
        this.adForm.isHasBindMobile = true
        this.submitForm('adFormRef')
      },

      changePrice () {
        this.priceChangeDialogVisable = false
        this.changeAppstoreType()
      },

      focusAppId () {
        console.log(this.$refs.ref_appid.value)
        this.adForm.oldappid = this.$refs.ref_appid.value
      }
    }
  }
</script>



