import {
  URI_APP_APPLIST, URI_APP_APPINFO,
  URI_APP_ASO_KEYWORD_RANK, URI_APP_KEYWORD_HISTORY_RANK,
  URI_APP_KEYWORD_ADD, URI_APP_KEYWORD_STICK,
  URI_APP_KEYWORD_CANCEL_STICK, URI_APP_KEYWORD_LIST,
  URI_APP_KEYWORD_CLEAR, URI_APP_KEYWORD_DELETE
} from '@/constants'
import api from '@/fetch'
import _ from 'lodash'
const types = {
  SYNC_TABLE: 'sync',
  SYNC_KEYWORD_CHART: 'sync_keyword_chart',
  DO_GET_MY_APP: 'doGetMyApp',
  DO_GET_APP_DETAIL: 'do_get_app_detail',
  DO_GET_TABLE_DATA: 'do_get_table_data',
  DO_SELECT_APP: 'do_select_app',
  DO_CURRENT_CHANGE: 'do_current_change',
  DO_HANDLE_CAR_CHANGE: 'do_handle_car_change',
  DO_ADD_CAR: 'do_add_car',
  DO_ADD_TO_TOP: 'do_add_to_top',
  DO_DELETE: 'do_delete',
  DO_GO_CAR: 'do_go_car',
  DO_EMPTY_CAR: 'do_empty_car',
  DO_FILTER_TABLE_DATA: 'do_filter_table_data',
  DO_FILTER_TABLE_DATA_KEYWORD: 'do_filter_table_data_keyword',
  DO_REMOVE_TO_TOP: 'do_remove_to_top',
  DO_THROW_BTN: 'do_throw_btn',
  DO_SET_APPID: 'do_set_appid',
  DO_SET_CAR_LIST: 'do_set_car_list',
  DO_CLEAR_TABLE_DATA: 'do_clear_table_data'
}

const initData = () => {
  return {

    // 加载中
    loading: true,

    // 下拉框是否可选
    disabled: false,

    // 我的应用列表
    myApp: [],

    // 我的应用信息
    myAppDetail: {},

    // 获取默认表格数据
    tableData: [],

    // 筛选结果数据
    inputFilterTableData: [],

    // 当前页数据
    newPageData: [],

    // 当前购物车页数据
    newCarPageData: [],

    // 购物车
    car: [],

    // 数据总数
    total: 0,

    // 当前页面
    state_currentPage: 0,

    // 搜索指数最大数
    state_search_max: 0,

    // 结果数最大数
    state_result_max: 0,

    // 搜索结果
    searchTableData: [],

    // 图表数据
    chart: {
      data: [],
      labels: []
    }
  }
}

const state = initData()

const getters = {
  chartLabels: state => {
    let labels = []
    _.each(state.chart.labels, l => {
      labels.push(l.substr(5, 8).replace('-', '.') + '点')
    })

    return labels
  },
  chartData: state => {
    return state.chart.data
  }
}

const mutations = {
  [types.SYNC_TABLE] (state, payload) {
    Object.assign(state.myApp, payload)
  },
  [types.SYNC_KEYWORD_CHART] (state, payload) {
    state.chart.labels = payload.labels
    state.chart.data = payload.data
  },
  [types.DO_SET_APPID] (state, appid) {
    state.loading = true
  },
  [types.DO_GET_MY_APP] (state, payload) {
    state.myApp = payload.list
  },
  [types.DO_GET_APP_DETAIL] (state, payload) {
    state.myAppDetail = payload
  },
  [types.DO_SET_CAR_LIST] (state, payload) {
    state.car = payload
  },
  [types.DO_SELECT_APP] (state) {
    state.loading = true
    state.newPageData = []
    state.car = []
    state.total = 0
    state.disabled = true
  },
  [types.DO_GET_TABLE_DATA] (state, payload) {
    if (payload) {
      state.state_currentPage = 1
    }
    state.loading = false
    state.disabled = false
    state.tableData = payload || state.tableData
    for (let i in state.tableData) {
      state.tableData[i].id = i
    }

    let rankArr = []
    let resultTableData = state.tableData.slice(0, 50)
    for (let i in resultTableData) {
      if (resultTableData[i].stick_rank > 0) {
        rankArr.push(resultTableData[i])
        resultTableData.splice(i, 1)
      }
    }

    state.newPageData = rankArr.concat(resultTableData)
    state.total = state.tableData.length

    // 设置表单最大最小值
    let sSearch = []
    let sResult = []
    for (let i in state.tableData) {
      sSearch.push(state.tableData[i].priority)
      sResult.push(state.tableData[i].result_count)
    }
    state.state_search_max = Math.max.apply(Math, sSearch)
    state.state_result_max = Math.max.apply(Math, sResult)

    console.log('state.total:' + state.total)
  },
  [types.DO_CURRENT_CHANGE] (state, page) {
    state.state_currentPage = page
    let rankData = (data) => {
      state.newPageData = data.slice((page - 1) * 50, page * 50).sort((a, b) => {
        return b.stick_rank - a.stick_rank
      })
    }
    if (state.inputFilterTableData.length) {
      rankData(state.inputFilterTableData)
    } else if (state.searchTableData.length) {
      rankData(state.searchTableData)
    } else {
      rankData(state.tableData)
    }
  },
  [types.DO_HANDLE_CAR_CHANGE] (state, page) {
    state.newCarPageData = state.car.slice((page - 1) * 7, page * 7)
  },
  [types.DO_ADD_CAR] (state, params) {
    params.row.id = params.id
    params.row.show_add = 0
    state.car.push(params.row)
    state.newCarPageData = state.car.slice(0, 7)
  },
  [types.DO_REMOVE_TO_TOP] (state, params) {
    params.row.stick_rank = 0
    if (state.newPageData.length === 1) {
      return
    } else {
      state.newPageData.splice(params.index, 1)

      let rowId = parseInt(params.row.id)
      let lasId = parseInt(state.newPageData[state.newPageData.length - 1].id) + 1
      let lastItem = state.newPageData[state.newPageData.length - 1]

      if (rowId === lasId || parseInt(lastItem.stick_rank) || lastItem.id < rowId) {
        state.newPageData.push(params.row)
        return
      } else {
        for (let i in state.newPageData) {
          if (state.newPageData[i].id > rowId && !parseInt(state.newPageData[i].stick_rank)) {
            state.newPageData.splice(i, 0, params.row)
            return
          }
        }
      }
    }
  },
  [types.DO_ADD_TO_TOP] (state, params) {
    params.row.stick_rank = 1
    state.newPageData.splice(params.index, 1)
    state.newPageData.unshift(params.row)
  },
  [types.DO_DELETE] (state, params) {
    state.car.splice(params.index, 1)
    state.newCarPageData.splice(params.index, 1)
    state.newCarPageData = state.car.slice(0, 7)
    for (let i in state.tableData) {
      if (state.tableData[i].keyword === params.row.keyword) {
        state.tableData[i].show_add = 1
        return
      }
    }
  },
  [types.DO_GO_CAR] (state, params) {
    state.newCarPageData = state.car.slice(0, 7)
  },
  [types.DO_EMPTY_CAR] (state) {
    state.car.splice(0, state.car.length)
    state.newCarPageData.splice(0, state.newCarPageData.length)
    for (let i in state.tableData) {
      if (!state.tableData[i].show_add) state.tableData[i].show_add = 1
    }
  },
  [types.DO_FILTER_TABLE_DATA] (state, params) {
    state.inputFilterTableData = state.tableData.filter((v) => {
      return v.rank >= params.rank_min && v.rank <= params.rank_max && params.search_max >= v.priority && params.search_min <= v.priority && params.result_max >= v.result_count && params.result_min <= v.result_count
    })
    state.total = state.inputFilterTableData.length
    state.newPageData = state.inputFilterTableData.slice(0, 50)
    console.log('state.total:' + state.total)
  },
  [types.DO_FILTER_TABLE_DATA_KEYWORD] (state, keyword) {
    state.searchTableData = []

    let keywordArr = []

    if (keyword.indexOf(',') > 0) {
      keywordArr = keyword.split(',')
    } else if (keyword.indexOf('，') > 0) {
      keywordArr = keyword.split('，')
    } else {
      keywordArr[0] = keyword
    }

    for (let k of keywordArr) {
      for (let i of state.tableData) {
        if (i.keyword.indexOf(k) >= 0) {
          state.searchTableData.push(i)
        }
      }
    }
    state.total = state.searchTableData.length
    state.newPageData = state.searchTableData.slice(0, 50)

    // 搜索之后处理置顶
    let rankArr = []
    for (let i in state.newPageData) {
      if (state.newPageData[i].stick_rank > 0) {
        console.log(i)
        rankArr.push(state.newPageData[i])
        state.newPageData.splice(i, 1)
      }
    }
    state.newPageData = rankArr.concat(state.newPageData)
  },
  [types.DO_THROW_BTN] (state) {
    state.searchTableData = []
    state.inputFilterTableData = []
  },
  [types.DO_CLEAR_TABLE_DATA] (state) {
    state.newPageData = []
  }
}

const actions = {

  // 获取我的应用列表
  doGetMyApp ({commit}, params) {
    let uri = `${URI_APP_APPLIST}?keyword=${params.keyword}&page=${params.page}`
    api(uri).then((res) => {
      commit(types.DO_GET_MY_APP, res.payload)
    })
  },

  getKeywordChart ({commit}, params = {appid: '', keyword: '', from_time: new Date(), to_time: Date.now()}) {
    let keyword = encodeURIComponent(params.keyword)
    let uri = `${URI_APP_KEYWORD_HISTORY_RANK}?appid=${params.appid}&keyword=${keyword}&from_time=${params.from_time}&to_time=${params.to_time}`
    return api(uri).then(({payload}) => {
      console.log(URI_APP_KEYWORD_HISTORY_RANK, payload)
      commit(types.SYNC_KEYWORD_CHART, {
        labels: payload.data.x_data,
        data: payload.data.y_data
      })
    })
  },

  // 获取应用详情
  doGetAppDetail ({commit}, params) {
    let uri = `${URI_APP_APPINFO}?appid=${params.appid}`
    api(uri).then((res) => {
      commit(types.DO_GET_APP_DETAIL, res.payload)
    })
    // 获取购物车列表
    let car = `${URI_APP_KEYWORD_LIST}?appid=${params.appid}`
    api(car).then((res) => {
      commit(types.DO_SET_CAR_LIST, res.payload.data)
    })
  },

  // 获取aso关键词列表
  doGetTableData ({dispatch, commit}, params) {
    commit(types.DO_SET_APPID, params.appid)
    let uri = `${URI_APP_ASO_KEYWORD_RANK}?appid=${params.appid}`
    return api(uri).then((res) => {
      commit(types.DO_GET_TABLE_DATA, res.payload.data, params.appid)
      if (params.isTop10 === 1) {
        dispatch('doFilterTableData', {
          keyword: '',
          rank_max: 10,
          rank_min: 1,
          result_max: state.state_result_max,
          result_min: 0,
          search_max: state.state_search_max,
          search_min: 0
        }).then(() => {
        })
      }
    })
  },

  // 选择下拉框
  doSelectApp ({commit}) {
    commit(types.DO_SELECT_APP)
  },

  // 获取分页
  doCurrentChange ({commit}, page) {
    commit(types.DO_CURRENT_CHANGE, page)
  },

  // 点击购物车分页
  doHandleCarChange ({commit}, page) {
    commit(types.DO_HANDLE_CAR_CHANGE, page)
  },

  // 添加购物车
  doAddCar ({commit}, params) {
    let body = {
      appid: params.appid,
      keyword: params.row.keyword
    }

    api(URI_APP_KEYWORD_ADD, {
      method: 'POST',
      body: body
    }).then(res => {
      commit(types.DO_ADD_CAR, {
        row: params.row,
        id: res.payload.id
      })
    })
  },

  // 置顶
  doAddToTop ({commit}, params) {
    let body = {
      keyword: params.row.keyword,
      appid: params.appid
    }
    api(URI_APP_KEYWORD_STICK, {
      method: 'POST',
      body: body
    }).then(res => {
      commit(types.DO_ADD_TO_TOP, params)
    })
  },

  // 取消置顶
  doRemoveToTop ({commit}, params) {
    let body = {
      keyword: params.row.keyword,
      appid: params.appid
    }
    api(URI_APP_KEYWORD_CANCEL_STICK, {
      method: 'POST',
      body: body
    }).then(res => {
      commit(types.DO_REMOVE_TO_TOP, params)
    })
  },

  // 去购物车
  doGoCar ({commit}, params) {
    commit(types.DO_GO_CAR, params)
  },

  // 删除购物车中的数据
  doDelete ({commit}, params) {
    let body = {
      id: params.row.id
    }
    api(URI_APP_KEYWORD_DELETE, {
      method: 'POST',
      body: body
    }).then(res => {
      commit(types.DO_DELETE, params)
    })
  },

  // 清空购物车
  doEmptyCar ({commit}, params) {
    let body = {
      appid: params.appid
    }
    api(URI_APP_KEYWORD_CLEAR, {
      method: 'POST',
      body: body
    }).then(res => {
      commit(types.DO_EMPTY_CAR)
    })
  },

  // 过滤数据
  doFilterTableData ({commit}, params) {
    commit(types.DO_FILTER_TABLE_DATA, params)
  },

  // 关键字搜索
  doFilterTableDataKeyword ({commit}, keyword) {
    commit(types.DO_FILTER_TABLE_DATA_KEYWORD, keyword)
  },

  // 立即投放
  doThrowBtn ({commit}) {
    commit(types.DO_THROW_BTN)
  },

  // 清空表格数据
  clearTableData ({commit}) {
    commit(types.DO_CLEAR_TABLE_DATA)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



