import _ from 'lodash'

import {
  URI_APP_APPLIST,
  URI_APP_APPINFO,
  URI_APP_RANK
} from '@/constants'
import api from '@/fetch'
const types = {
  SYNC_APP_LIST: 'sync_app_list',
  SYNC_TIME_RANGE: 'sync_time_range',
  SYNC_CHART: 'sync_chart',
  TOGGLE_CHART_DATA: 'toggle_chart_data'
}

const initData = () => {
  return {
    // 图表数据
    chart: {
      data: [],
      data_sum: [],
      labels: []
    },
    startTime: '',
    endTime: '',
    // 我的应用列表
    appList: [],

    // 我的榜单信息
    rankList: [],

    // 我的应用信息
    myAppDetail: {},

    // 获取默认表格数据
    tableData: []

  }
}

const state = initData()

const getters = {
  chartLabels: state => {
    let labels = []
    // 当日期范围≤7天时，最小时间单位精确到小时
    // 当日期范围＞7天时，最小时间单位精确到天
    let offsetDay = parseInt((state.endTime - state.startTime) / (3600 * 24))
    if (offsetDay > 7) {
      _.each(state.chart.labels, l => {
        labels.push(l.substr(5, 5))
      })
    } else {
      _.each(state.chart.labels, l => {
        labels.push(l.substr(5, 8).replace('-', '.') + '点')
      })
    }
    return labels
  },
  chartData: state => {
    return {data: state.chart.data, data_sum: state.chart.data_sum}
  }
}

const mutations = {
  [types.SYNC_APP_LIST] (state, appList) {
    state.appList = appList
  },
  [types.SYNC_TIME_RANGE] (state, {startTime, endTime}) {
    state.startTime = startTime
    state.endTime = endTime
  },
  [types.TOGGLE_CHART_DATA] (state, {data, index}) {
    switch (data.genre_type) {
      case 1:
        if (state.chart.data_sum.length === 0) {
          state.chart.data_sum = data.y_data
        } else {
          state.chart.data_sum = []
        }
        break
      case 2:
        if (state.chart.data.length === 0) {
          state.chart.data = data.y_data
        } else {
          state.chart.data = []
        }
        break
    }
  },
  [types.SYNC_CHART] (state, payload) {
    if (payload.data.length > 0) {
      state.chart.labels = payload.data[0].x_data
      state.chart.data = payload.data[0].y_data
    }
    if (payload.data.length > 1) {
      state.chart.data_sum = payload.data[1].y_data
    }
  }
}

const actions = {
  // 获取应用下拉列表
  doGetMyApp ({commit}) {
    let uri = `${URI_APP_APPLIST}`
    api(uri).then((res) => {
      commit(types.SYNC_APP_LIST, res.payload.list)
    })
  },

  // 获取应用详情
  doGetAppDetail (commit, params) {
    let uri = `${URI_APP_APPINFO}?appid=${params.appid}`
    api(uri).then((res) => {
      state.myAppDetail = res.payload
    })
  },

  toggleChartData ({commit}, params) {
    commit(types.TOGGLE_CHART_DATA, params)
  },

  getChartData ({commit}, params) { // 按天数、按指标类型取图表数据
    let url = `${URI_APP_RANK}?appid=${params.appid}&from_time=${params.from_time}&to_time=${params.to_time}`
    return fetch(url, {
      credentials: 'same-origin'
    })
      .then(res => res.json())
      .then(data => {
        let payload = data && data.payload
        state.rankList = payload.data
        commit(types.SYNC_TIME_RANGE, {
          startTime: params.from_time,
          endTime: params.to_time
        })
        commit(types.SYNC_CHART, payload)
        return data
      })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



