/* eslint no-unused-vars: off */
import {Message} from 'element-ui'
import _ from 'lodash'

import {
  LW,
  L30D,
  L90D,
  URI_LOGOUT,
  URI_DASHBOARD,
  URI_REPORT,
  URI_REPORT_DOWNLOAD
} from '@/constants'

const types = {
  SYNC_DASHBOARD: 'sync_dashboard',
  SYNC_CHART: 'sync_chart',
  SYNC_TABLE: 'sync_table',
  SYNC_NAVBAR: 'sync_navbar'
}

const initState = () => ({
  searchForm: { // 按时间搜索时 选择的时间
    end_time: '',
    begin_time: ''
  },
  app_list: [],
  navbar: [],
  portlet: [],

  reportType: LW,
  reportTime: '',

  // 图表数据
  chart: {
    data: [],
    labels: []
  },

  table: {
    data: [],
    summary: {
      cost: 0,
      impression: 0,
      clicks: 0,
      click_rate: '0.00%',
      effect_actions: 0,
      effect_rate: '0.00%',
      zs_done_count: 0,
      zs_finish_total_count: 0
    }
  }
})

const state = initState()

const getters = {
  // indicesLastday: state => {
  //   return state.indices
  // },

  // indicesDBY: state => {
  //   return state.indices.dby
  // },

  reportApps: state => {
    return state.app_list
  },
  reportTypes: state => ([
    {label: '最近7天', value: LW},
    {label: '30天', value: L30D},
    {label: '90天', value: L90D}
  ]),

  reportSummary: state => {
    return state.table.summary
  },

  tableData: state => {
    return state.table.data
  },

  chartLabels: state => {
    let labels = []
    _.each(state.chart.labels, l => {
      if (l.substr(0, 1) === '0') l = l.substring(1)
      labels.push(l.replace('-', '月') + '日')
    })

    return labels
  },

  chartData: state => {
    return state.chart.data
  }
}

const mutations = {
  [types.SYNC_DASHBOARD] (state, payload) {
    // 用户名、余额、最大可透支金额
    state.navbar = payload.navbar

    // 应用名称列表
    state.app_list = payload.app_list

    // 首页汇总信息
    state.portlet = payload.portlet
  },

  [types.SYNC_CHART] (state, payload) {
    state.chart = payload
  },

  [types.SYNC_TABLE] (state, payload) {
    state.table = payload
  }
}

const actions = {
  getDashboardData ({commit, dispatch}) {
    return fetch(URI_DASHBOARD, {
      credentials: 'same-origin'
    })
      .then(res => {
        if (res.status === 401) {
          $router.push('/login')
        } else {
          return res
        }
      })
      .then(res => res.json())
      .then(data => {
        let payload = data && data.payload
        if (payload.navbar) dispatch('updateNavbar', payload, {root: true})
        if (payload) commit(types.SYNC_DASHBOARD, payload)
        return data
      })
      .catch(err => {
        console.log(err)
      })
  },

  getChartData ({commit}, payload) { // 按天数、按指标类型取图表数据
    let {content, endTime, startTime, appId} = payload
    let dayCnt = (endTime - startTime) / 86400
    if (!appId) appId = ''
    let url = `${URI_REPORT}?action=chart&content=${content}&day_cnt=${dayCnt}&ts_start=${startTime}&ts_end=${endTime}&app_id=${appId}`

    return fetch(url, {
      credentials: 'same-origin'
    })
      .then(res => res.json())
      .then(data => {
        let payload = data && data.payload
        commit(types.SYNC_CHART, payload)
        return data
      })
  },

  getTableData ({commit}, payload) {
    let {content, endTime, startTime, appId} = payload
    if (!appId) appId = ''
    let url = `${URI_REPORT}?action=table&content=${content}&ts_start=${startTime}&ts_end=${endTime}&app_id=${appId}`

    return fetch(url, {
      credentials: 'same-origin'
    })
      .then(res => res.json())
      .then(data => {
        let payload = data && data.payload
        commit(types.SYNC_TABLE, payload)
        return data
      })
  },
  getTableDataByTime ({commit}, payload) {
    let {content, endTime, startTime, appId} = payload
    if (!appId) appId = ''
    let url = `${URI_REPORT}?action=table&content=${content}&ts_start=${startTime}&ts_end=${endTime}&app_id=${appId}`

    return fetch(url, {
      credentials: 'same-origin'
    })
      .then(res => res.json())
      .then(data => {
        let payload = data && data.payload
        commit(types.SYNC_TABLE, payload)
        return data
      })
  },

  downloadReport (ctx, payload) {
    let {endTime, startTime, appId} = payload
    if (!appId) appId = ''
    location.href = `${URI_REPORT_DOWNLOAD}?ts_start=${startTime}&ts_end=${endTime}&app_id=${appId}`
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



