<template>
  <div class="addapply-container">

    <!-- 面包屑 -->
    <div class="breadcrumb">
      <span class="breadcrumb-item">
        <span class="breadcrumb-item-inner" @click="toFinance()">我的应用</span>
        <span class="breadcrumb-separator"></span>
      </span>
      <span class="breadcrumb-item">
        <span class="breadcrumb-item-inner" v-text="page_sub_title"></span>
      </span>
    </div>

    <!-- 搜索应用 -->
    <el-form class="search-wrapper" id="addAd-form-id">
      <el-form-item prop="title">
        <el-input class="large" v-model="keyword" @keyup.enter.native="searchApp(keyword)" placeholder="请输入应用标题"></el-input>
        <input type="text" style="display: none"> <!-- 解决上面input输入框输入回车后刷新页面的问题 -->
        <el-button style="margin-left: 10px; position: relative; top: -1px" @click="searchApp(keyword)" :disabled="disabled" type="primary" size="small">搜索</el-button>
      </el-form-item>
    </el-form>

    <el-row v-loading='dataLoading' element-loading-text="加载中...">
      <div class="app-wrapper">
        <div class="app-list" v-for="(item, index) in searchList">
          <div class="image">
            <img :src="item.icon" alt="">
          </div>
          <div class="title">
            <p v-text="item.title"></p>
            <span>{{ item.seller }}</span>
          </div>
          <div class="button">
            <el-button v-if="!item.added" size="min" type="primary" @click="add(index, item.appid, item)">添加</el-button>
            <div v-else>
              <el-button size="min" @click="del(index, item.appid, item)">删除</el-button>
              <p>已添加</p>
            </div>
          </div>
        </div>
      </div>
    </el-row>

    <el-dialog v-model="addSuccessDialogVisible" :show-close="showClose" custom-class="charge-success-dialog" style="top: 30%;">
      <img class="logo" src="//qianka.b0.upaiyun.com/images/425ec42718c6ef5cbe6e6fe998b66d12.png" alt="">
      <span class="qk-title">添加成功！</span>
      <span slot="footer" class="dialog-footer">
        <el-button size="flat" @click="ASO()">
          <p>推荐</p>
          <img class="logo" src="//assets.qkcdn.com/images/ae30a1d5aa885f0ae75c3886eba9fd00.png" alt="">
          ASO关键词分析
        </el-button>
        <el-button type="primary" size="flat" @click="forwardTo()">立即投放</el-button>
      </span>
    </el-dialog>

    <el-pagination
      v-if="searchList.length > 0"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="state_currentPage || 1"
      :page-size="30"
      :total="total"
      >
    </el-pagination>



  </div>
</template>
<style lang="scss">
  .addapply-container {
    width: 100%;
    padding: 50px 0 150px 35px;

    .breadcrumb {
      height: 22px;
      .breadcrumb-item {
        display: inline-block;
        float: left;
        line-height: 22px;
        margin-right: 10px;
        cursor: pointer;
        .breadcrumb-item-inner {
          font-family: PingFangSC-Light;
          font-size: 16px;
          line-height: 22px;
          color: #888888;
        }
        .breadcrumb-separator {
          width: 6px;
          height: 12px;
          margin-left: 5px;
          display: inline-block;
          background-image: url('//qianka.b0.upaiyun.com/images/a688c7dd7a765df07ec7d9cfab76b68f.png');
          background-size: 6px 12px;
          background-position: center;
          background-repeat: no-repeat;
        }
      }
      .breadcrumb-item:last-child {
        cursor: text;
      }
    }

    .search-wrapper {
      margin: 47px 35px 30px 0;
      text-align: center;
      border-bottom: 1px solid #f5f5f5;
    }

    .app-wrapper{
      height: auto;
      overflow: hidden;
      .app-list{
        display: flex;
        width: 50%;
        padding: 0 55px 30px 55px;
        float: left;
        border-right: 1px solid #f5f5f5;
        &:nth-child(odd) {
          padding-left: 0
        }
        &:nth-child(even) {
          border-right: 0 none;
        }
        &:last-child {
          padding-bottom: 0;
          margin-bottom: 30px;
        }
        &:nth-last-child(2) {
          padding-bottom: 0;
          margin-bottom: 30px;
        }
        .image{
          width: 50px;
          border: solid 0.5px rgba(58, 58, 58, 0.2);
          height: 50px;
          overflow: hidden;
          border-radius: 6px;
          float: left;
          margin-right: 15px;
          img{
            width: 50px;
            height: 50px;
            border-radius: 5px;
          }
        }
        .title{
          flex: 1;
          height: 50px;
          padding-top: 6px;
          overflow:hidden;
          white-space:nowrap;
          text-overflow:ellipsis;
          p{
            display: block;
            overflow:hidden;
            white-space:nowrap;
            text-overflow:ellipsis;
            font-size: 14px;
            font-weight: 600;
            color: #3a3a3a;
            margin: 0;
            line-height: 18px;
          }
          span{
            display: block;
            padding-top: 9px;
            font-size: 13px;
            margin: 0;
            color: #888888;
          }
        }
        .button{
          width: 150px;
          .hidden {
            display: none;
          }
          p{
            line-height: 50px;
            font-size: 13px;
            float: right;
            margin: 0;
            color: #888888;
          }
          .el-button {
            float: right;
            margin: 9px 0 9px 17px;
          }
        }
      }
    }
    .hide:last-child {
      display: none
    }

    .el-dialog {
      width: 390px;
      height: 152px;
      padding: 30px;
      .el-dialog__header {
        .el-dialog__title {
          line-height: 22px;
        }
        .el-dialog__headerbtn {
          z-index: 99;
          position: relative;
          .el-dialog__close {
            color: #B5B5B5;
            font-size: 14px;
          }
        }
      }
      .el-dialog__body {
        padding: 0 0 0 45px;
        .logo {
          top: 0px;
        }
        .qk-title {
          position: relative;
          top: 7px;
          font-size: 16px;
        }
      }

      .dialog-footer {
        padding-top: 25px;
        position: relative;
        display: block;
        text-align: right;
        p {
          position: absolute;
          top: -17px;
          left: 116px;
          width: 40px;
          height: 24px;
          line-height: 24px;
          border-radius: 2px;
          color: #fff;
	        background-color: #de4948;
          &:after {
            color: #de4948;
            position: absolute;
            top: 15px;
            left: 12px;
          }
        }
        img {
          position: absolute;
          top: 19px;
          left: 125px;
        }
      }
    }

    .el-loading-mask {
      top: 35px;
    }
  }
</style>
<script>
  import { mapState, mapActions } from 'vuex'

  export default {
    components: {
    },

    data () {
      return {
        dataLoading: false,
        disabled: false,
        addClass: '',
        keyword: '',
        state_appid: '',
        page_sub_title: '添加应用',
        addSuccessDialogVisible: false,
        showClose: true
      }
    },

    mounted () {
      // this.addButton = true
      // this.delButton = true
      this.keyword = this.state_keyword
    },

    computed: {
      ...mapState('add', [
        'appList',
        'total',
        'searchList',
        'state_currentPage',
        'state_keyword'
      ])
    },

    methods: {
      ...mapActions('add', [
        'doCurrentChange',
        'addApp',
        'deleteApp',
        'search'
      ]),
      searchApp (keyword) {
        if (this.keyword === '') {
          return
        }
        this.dataLoading = true
        this.disabled = true
        return this.search({
          keyword: this.keyword
        }).then(() => {
          this.doCurrentChange(1)
          this.dataLoading = false
          this.disabled = false
        }).catch(() => {
          this.dataLoading = false
          this.disabled = false
        })
      },
      handleCurrentChange (currentPage) {
        this.dataLoading = true
        this.disabled = true
        return this.search({
          keyword: this.keyword,
          page: currentPage
        }).then(() => {
          this.dataLoading = false
          this.disabled = false
        })
      },
      // 添加应用
      add (index, appid, item) {
        this.state_appid = appid
        this.addApp({
          index: index,
          appid: appid
        }).then(() => {
          item.added = !item.added
          this.addSuccessDialogVisible = true
        })
      },
      // 删除应用
      del (index, appid, item) {
        this.deleteApp({
          appid: appid
        }).then(() => {
          item.added = !item.added
        })
      },
      ASO () {
        this.$router.push('/d/app/aso_keyword/' + this.state_appid)
      },
      forwardTo () {
        this.$router.push('/d/ad/new/' + this.state_appid)
      }
    }
  }
</script>



