<template>
  <div class="asokeyword-container aso_keyword">

    <!-- nav -->
    <div class="nav">
      <div class="title" @click="$router.push('/d/app/list')">我的应用</div>
      <div class="right"><img src="http://qianka.b0.upaiyun.com/images/a688c7dd7a765df07ec7d9cfab76b68f.png"></div>
      <el-select
        class="small"
        popper-class="mini"
        v-model="title"
        :placeholder="myAppDetail.title | regexTitle"
        :disabled="disabled"
        @change="selectApp">
        <el-option
          v-for="(item, index) in myApp"
          :key="index"
          :label="item.title | regexTitle"
          :value="item.appid">
        </el-option>
      </el-select>
    </div>
    <!-- nav -->

    <!-- information -->
   <app-info :app-detail="myAppDetail"></app-info>
    <!-- information -->

    <!-- tabs -->
    <el-tabs v-model="activeName" class="qk-tabs-text" type="card" @tab-click="handleClick">
      <el-tab-pane name="first">
        <span class="qk-tabs__item-text" slot="label"><span class="tag">榜单排名</span></span>
      </el-tab-pane>
      <el-tab-pane name="seco">
        <span class="qk-tabs__item-text" slot="label"><span>ASO关键词</span></span>
      </el-tab-pane>
    </el-tabs>
    <!-- tabs -->

    <!-- search -->
    <div class="search">
      <section>
        <span>排名</span>
        <el-input
          v-model="rank_min"
          @change="filterTableData"
          placeholder="最小值"
          class="small"></el-input>
        <i>-</i>
        <el-input
          v-model="rank_max"
          @change="filterTableData"
          placeholder="最大值"
          class="small"></el-input>
      </section>
      <section>
        <span class="m-l20">搜索指数</span>
        <el-input
          v-model="search_min"
          @change="filterTableData"
          placeholder="最小值"
          class="small"></el-input>
        <i>-</i>
        <el-input
          v-model="search_max"
          @change="filterTableData"
          placeholder="最大值"
          class="small"></el-input>
      </section>
      <section>
        <span class="m-l20">结果数</span>
        <el-input
          v-model="result_min"
          @change="filterTableData"
          placeholder="最小值"
          class="small"></el-input>
        <i>-</i>
        <el-input
          v-model="result_max"
          @change="filterTableData"
          placeholder="最大值"
          class="small"></el-input>
      </section>
      <section>
        <span class="m-l20">关键词</span>
        <el-input
          v-model="keyword"
          placeholder="多个词同时搜索用“，”隔开"
          @keyup.enter.native="filterTableDataKeyword(keyword)"
          style="width: 190px; text-align: left;"></el-input>
      </section>
      <section>
        <el-button class="search-btn" type="primary" size="small" @click="filterTableDataKeyword(keyword)">搜索</el-button>
      </section>
    </div>
    <!-- search -->
    <!-- car -->
    <div class="car" v-if="!loading">
      <div class="btn" @click="goCar()">去购物车 <span>{{car.length}}</span></div>
    </div>
    <!-- car -->
    <!-- table -->
    <el-table
      ref="keywordTable"
      class="table"
      :class="{'nodata': newPageData.length === 0}"
      :data="newPageData"
      :row-key="rowKey"
      :expand-row-keys="expandRowKeys"
      v-loading="loading"
      border
      stripe
      style="width: 100%">
      <el-table-column type="expand">
        <template scope="scope">
          <el-date-picker class="medium" type="daterange"
                          @change="dateRangePicker"
                          align="right" v-model="dateSelectedRangeArr" placeholder="选择日期范围"
                          :picker-options="pickerOptions2" format="yyyy.MM.dd" range-separator="-">
          </el-date-picker>
          <span class="title">{{ dateSelectedRangeStr }}“{{ selectedRow.keyword }}”在搜索结果中排名趋势</span>
          <!-- 表格图表 -->
          <div id="table-chart"></div>
          <!-- 表格图表 -->
        </template>
      </el-table-column>
      <el-table-column
        width="196px"
        prop="keyword"
        label="关键词">
      </el-table-column>
      <el-table-column
        prop="rank"
        label="最新排名">
      </el-table-column>
      <el-table-column label="变动">
        <template scope="scope">
          <div>
            <span
              v-if="scope.row.rank_trend > 0"
              class="color-de4948">
              上升 {{scope.row.rank_trend}}
            </span>
            <span v-if="scope.row.rank_trend == 0">
              -
            </span>
            <span v-if="scope.row.rank_trend < 0"
              class="color-6bad5d">
              下降 {{Math.abs(scope.row.rank_trend)}}
            </span>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        prop="priority"
        label="搜索指数">
      </el-table-column>
      <el-table-column
        prop="result_count"
        label="结果数">
      </el-table-column>
      <el-table-column width="207px" label="操作" class-name="not-default-space">
        <template scope="scope">
          <div>
            <a @click="showRank(scope.row)" class="color-4a90e2" href="javascript:;">排名</a>
            <a v-if="scope.row.stick_rank" @click="removeToTop(scope.$index, scope.row)" class="m-l18 color-ddd" href="javascript:;">取消</a>
            <a v-else @click="addToTop(scope.$index, scope.row)" class="m-l18 color-4a90e2" href="javascript:;">置顶</a>
            <a v-if="scope.row.show_add" @click="addToCar(scope, scope.$index, scope.row, $event)" class="m-l18 color-4a90e2" href="javascript:;">添加</a>
            <a v-else class="m-l18 color-ddd" href="javascript:;">已添加</a>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <!-- table -->

    <!-- el-pagination -->
    <el-pagination
      layout="prev, pager, next"
      v-if="total && newPageData.length"
      @current-change="handleCurrentChange"
      :page-size="50"
      :current-page='state_currentPage || 1'
      :total="total">
    </el-pagination>
    <!-- el-pagination -->

    <!-- 购物车 -->
    <el-dialog
      title="已添加关键词"
      :visible.sync="dialogVisible"
      class="dialogVisible">

      <!-- el-table -->
      <el-table
        class="right"
        :data="newCarPageData"
        border
        style="width: 100%;">
        <el-table-column
          prop="keyword"
          width="141px"
          label="关键词">
        </el-table-column>
        <el-table-column
          prop="priority"
          label="搜索指数">
        </el-table-column>
        <el-table-column
          prop="rank"
          width="89px"
          label="当前排名">
        </el-table-column>
        <el-table-column label="操作" width="63px">
          <template scope="scope">
            <div>
              <a @click="deleteCar(scope.$index, scope.row)" class="color-4a90e2" href="javascript:;">删除</a>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- el-table -->

      <!-- el-pagination -->
      <el-pagination
        layout="prev, pager, next"
        v-if="car.length"
        :total="car.length"
        :page-size="7"
        @current-change="handleCarChange">
      </el-pagination>
      <!-- el-pagination -->

      <!-- btn-group -->
      <div class="btn-group" v-if="car.length">
        <el-button size="medium" @click="emptyCar">清空</el-button>
        <el-button type="primary" size="medium" @click="throwBtn(car)">立即投放</el-button>
      </div>
      <!-- btn-group -->

    </el-dialog>
    <!-- 购物车 -->

    <!-- 购物车动画 -->
    <div class="circle" :class="{moveCircle:circleClass}" ref="circle"></div>
    <!-- 购物车动画 -->

  </div>
</template>
