<script>
import {mapState, mapGetters, mapActions} from 'vuex'
import AppInfo from './App-Info.vue'
// import _ from 'lodash'
export default {
  components: {
    AppInfo
  },
  data () {
    return {

      // 移动动画
      circleClass: false,

      // 默认appid
      appid: '',

      // 我的应用
      title: '',

      // 购物车弹出层
      dialogVisible: false,

      // aso关键词选项卡
      activeName: 'seco',

      showRankID: '',

      // 搜索表单
      rank_min: '',
      rank_max: '',
      search_min: '',
      search_max: '',
      result_min: '',
      result_max: '',
      keyword: '',
      pickerOptions2: { // 搜索时间的配置
        shortcuts: [{
          text: '近24小时',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '近三日',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 2)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '近七日',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 6)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近一个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 29)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      expandRowKeys: [],
      selectedRow: {},
      dateSelectedRangeArr: [],
      dateSelectedRangeStr: '近3日'
    }
  },

  computed: {
    ...mapState('asoKeyword', [
      'loading',
      'isTop10',
      'disabled',
      'myApp',
      'myAppDetail',
      'tableData',
      'newPageData',
      'newCarPageData',
      'inputFilterTableData',
      'total',
      'state_currentPage',
      'car',
      'state_search_max',
      'state_result_max'
    ]),
    ...mapGetters('asoKeyword', [
      'chartLabels',
      'chartData'
    ])
  },

  mounted () {
    this.appid = this.$route.params.appid
    // 清空表格数据
    this.clearTableData()
    // 获取我的应用列表
    this.getMyApp(this.appid)
    // 获取我的应用信息
    this.getAppDetail(this.appid)
    // 获取默认表格数据
    this.getTableData(this.appid)

    window.addEventListener('resize', this.fetchKeywordChart)
  },

  distoryed () {
    window.removeEventListener('resize', this.fetchKeywordChart)
  },

  methods: {
    ...mapActions('asoKeyword', [
      'getKeywordChart',
      'doGetMyApp',
      'doGetTableData',
      'doGetAppDetail',
      'doSelectApp',
      'doCurrentChange',
      'doHandleCarChange',
      'doFilterTableData',
      'doFilterTableDataKeyword',
      'doEmptyCar',
      'doDelete',
      'doAddCar',
      'doAddToTop',
      'doRemoveToTop',
      'doThrowBtn',
      'doGoCar',
      'add',
      'clearTableData'
    ]),

    dateRangePicker (val) {
      // 时间范围选择
      if (!this.dateSelectedRangeArr || !this.dateSelectedRangeArr[0]) return // 日期选择框点X时，数组值会变成null，下面JS会报错
      let timeOffset = Date.parse(this.dateSelectedRangeArr[1]) - Date.parse(this.dateSelectedRangeArr[0])
      timeOffset = timeOffset / (3600 * 1000)
      let str = val
      switch (timeOffset) {
        case 24:
          str = '近24小时'
          break
        case 24 * 2:
          str = '近三日'
          break
        case 24 * 6:
          str = '近七日'
          break
        case 24 * 29:
          str = '近一月'
          break
      }
      this.dateSelectedRangeStr = str
      this.fetchKeywordChart()
    },

    rowKey (row) {
      return row.id
    },

    // 点击表格排名
    showRank (row) {
      // 设置时间参数
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 3)
      this.dateSelectedRangeArr = [start, end]

      // 判断是否有展开的列
      if (this.expandRowKeys.length) {
        if (this.showRankID === row.id) {
          this.selectedRow = []
          this.expandRowKeys = []
          // 关闭后设置下次点开期限
          this.dateSelectedRangeStr = '近三日'
          // 关闭需要拿掉时间选择框的值，否则resize会触发图标重绘，拿不到容器报错
          this.dateSelectedRangeArr = []
        } else {
          this.showRankID = row.id
          this.selectedRow = row
          this.expandRowKeys = [row.id]
          this.fetchKeywordChart()
        }
      } else {
        this.showRankID = row.id
        this.selectedRow = row
        this.expandRowKeys = [row.id]
        this.fetchKeywordChart()
      }
    },

    fetchKeywordChart () {
      if (this.dateSelectedRangeArr.length) {
        let keyword = this.selectedRow.keyword || ''
        let appid = this.$route.params.appid || ''
        let fromTime = parseInt(Date.parse(this.dateSelectedRangeArr[0]) / 1000)
        let toTime = parseInt(Date.parse(this.dateSelectedRangeArr[1]) / 1000)
        this.getKeywordChart({
          appid,
          keyword,
          from_time: fromTime,
          to_time: toTime
        }).then(() => {
          // 初始化echarts实例
          this.drawChart(this.chartLabels, this.chartData)
        })
      }
    },

    drawChart (x, y) {
      // 初始化echarts实例
      let myChart = window.echarts.init(document.getElementById('table-chart'))
      // 自适应
      myChart.resize()
      // 清空上次的图表数据
      myChart.clear()
      myChart.setOption({
        tooltip: {
          trigger: 'axis',
          left: 'right',
          backgroundColor: 'rgba(0, 0, 0, 0.05)',
          textStyle: {
            color: '#888',
            fontSize: 13,
            fontWeight: '600'
          },
          padding: 12
        },
        toolbox: {
          right: '23x',
          feature: {
            saveAsImage: {}
          }
        },
        grid: {
          left: '15px',
          right: '5px',
          bottom: '30px',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          data: x,
          boundaryGap: false,
          axisLine: {
            show: false,
            lineStyle: {
              color: '#b5b5b5'
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              type: 'solid',
              color: '#e2e2e2'
            }
          },
          axisTick: false
        },
        yAxis: [{
          type: 'value',
          inverse: true,
          min: 1,
          minInterval: 1,
          max: () => {
            return Math.max.apply(Math, y) + 1
          },
          axisLine: {
            lineStyle: {
              opacity: 0,
              color: '#b5b5b5'
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              type: 'solid',
              color: '#e2e2e2'
            }
          },
          axisTick: false
        },
        {
          axisLine: {
            show: true,
            lineStyle: {
              type: 'solid',
              color: '#e2e2e2'
            }
          }
        }],
        series: [{
          name: '应用排名',
          data: y,
          type: 'line',
          symbol: 'circle',
          symbolSize: 4,
          lineStyle: {
            color: '#de4948'
          }
        }]
      })
    },

    destroyed () {
      window.removeEventListener('resize', this.fetchKeywordChart)
    },

    // 跳转到榜单排名
    handleClick (tab, event) {
      if (tab.index === '1') {
        return
      } else {
        this.$nextTick(() => {
          this.$router.push('/d/app/rank/' + this.appid)
        })
      }
    },

    // 选择下拉框
    selectApp (appid) {
      // 设置appid
      this.appid = appid
      // 改变url
      this.$router.push('/d/app/aso_keyword/' + appid)
      // 选择下拉框
      this.doSelectApp()
      // 获取应用详情
      this.getAppDetail(appid)
      // 获取表格数据
      this.doGetTableData({
        appid: appid
      })
    },

    // 筛选数据
    filterTableData () {
      // 默认回到首页
      this.doCurrentChange(1)

      // 筛选数据
      this.doFilterTableData({
        rank_min: this.rank_min || 0,
        rank_max: this.rank_max || 1500,
        search_min: this.search_min || 0,
        search_max: this.search_max || this.state_search_max,
        result_min: this.result_min || 0,
        result_max: this.result_max || this.state_result_max,
        keyword: this.keyword || ''
      })
    },

    // 关键字搜索
    filterTableDataKeyword (keyword) {
      // 默认回到首页
      this.doCurrentChange(1)

      // 筛选关键字
      this.doFilterTableDataKeyword(keyword)

      // 清空表单筛选
      this.rank_min = this.rank_max = this.search_min = this.search_max = this.result_min = this.result_max = ''
    },

    // 获取我的应用列表
    getMyApp () {
      this.doGetMyApp({
        keyword: '',
        page: 1
      })
    },

    // 获取应用详情
    getAppDetail (appid) {
      this.doGetAppDetail({
        appid: appid
      })
    },

    // 获取默认表格数据
    getTableData (appid) {
      let isTop10Lo = 0
      if (this.$route.query.isTop10) {
        this.rank_min = 1
        this.rank_max = 10
        this.isTop10 = true
        isTop10Lo = 1
      }
      this.doGetTableData({
        appid: appid,
        isTop10: isTop10Lo
      })
    },

    // 点击分页
    handleCurrentChange (currentPage) {
      this.doCurrentChange(currentPage)
    },

    // 点击购物车分页
    handleCarChange (currentPage) {
      this.doHandleCarChange(currentPage)
    },

    // 添加到购物车
    addToCar (scope, index, row, ev) {
      if (this.car.length === 100) {
        this.$message.error('您添加的关键词数已达上限，请在清理后继续添加')
      } else {
        if (row.rank > 150) {
          this.$message.error('您的关键词排名过低，请重新选择排名150以内的关键词')
        } else {
          this.doAddCar({
            row: row,
            appid: this.appid
          })

          this.circleClass = false
          this.$refs.circle.style.left = ev.clientX + 'px'
          this.$refs.circle.style.top = ev.clientY + 'px'

          setTimeout(() => {
            this.circleClass = true
          }, 300)
        }
      }
    },

    // 置顶
    addToTop (index, row) {
      this.doAddToTop({
        index: index,
        row: row,
        appid: this.appid
      })
    },

    // 取消置顶
    removeToTop (index, row) {
      this.doRemoveToTop({
        index: index,
        row: row,
        appid: this.appid
      })
    },

    // 删除购物车中的数据
    deleteCar (index, row) {
      if (this.car.length === 1) this.dialogVisible = false
      this.doDelete({
        index: index,
        row: row
      })
    },

    // 去购物车
    goCar () {
      if (this.car.length) {
        this.dialogVisible = true
        this.doGoCar({
          appid: this.appid
        })
      }
    },

    // 清空购物车
    emptyCar () {
      this.dialogVisible = false
      this.doEmptyCar({
        appid: this.appid
      })
    },

    // 立即投放
    throwBtn (car) {
      this.doThrowBtn()
      this.$router.push('/d/ad/new/' + this.$route.params.appid)
    }
  },

  filters: {
    // 格式化标题
    regexTitle (val) {
      if (val) {
        val = val.split('·')[0]
        val = val.split('－')[0]
        val = val.split('—')[0]
      }
      return val ? val.split('-')[0] : val
    }
  }
}
</script>



