<template>
  <div class="ad-app-list">
    <el-breadcrumb>
      <el-breadcrumb-item>我的应用</el-breadcrumb-item>
    </el-breadcrumb>
    <div style="margin-top: 47px;">
      <el-input v-model="appName" class="small" placeholder="按应用名查找"></el-input>
      <el-button @click="searchApp()" style="margin-left: 10px;vertical-align: top;" type="primary" size="small">搜索</el-button>
      <el-button class="addAd-btn" @click="forwardTo('app_add')">
        <img class="add-logo" src="//qianka.b0.upaiyun.com/images/62ebcde46a8f43cedce613491089a1b0.png" alt="">
        添加应用</el-button>
    </div>
    <div class="table-wrapper">
      <el-table style="width: 100%;" :class="{'nodata': tableData.length === 0}" :data="tableData" stripe border @cell-click="cellClick"
                empty-text="尚未添加任何应用" v-loading="dataLoading"  element-loading-text="加载中...">
        <el-table-column prop="title" label="应用名">
        </el-table-column>
        <el-table-column prop="appid" label="AppID">
        </el-table-column>
        <el-table-column prop="genres_main_rank" label="总榜">
        </el-table-column>
        <el-table-column prop="genres_class_rank" label="分类榜">
        </el-table-column>
        <el-table-column min-width="94px" prop="num_keywords" label="全部关键词覆盖" class-name="cursor-point">
        </el-table-column>
        <el-table-column min-width="94px" prop="top10_keywords" label="Top10关键词覆盖" class-name="cursor-point">
        </el-table-column>
        <el-table-column width="178px" label="效果分析" class-name="not-default-space">
          <template scope="scope">
            <div style="height: 22px;">
              <a @click="forwardTo('app_rank', scope.row)" href="javascript:;">榜单排名</a>
              <a class="mrg-l18" @click="forwardTo('app_keyword', scope.row)" href="javascript:;">ASO关键词</a>
            </div>
          </template>
        </el-table-column>
        <el-table-column width="140px" label="操作" class-name="not-default-space">
          <template scope="scope">
            <div style="height: 22px;">
              <a @click="forwardTo('app_on', scope.row)" href="javascript:;">立即投放</a>
              <a @click="reviewDelApp({appid: scope.row.appid, index: scope.$index})" class="mrg-l18" href="javascript:;">删除</a>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      v-if="totalApps > 0"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="10"
      :total="totalApps"
    >
    </el-pagination>

    <confirm-del-dialog ref="delConfirmDialog" :handle-delete="handleDelete"></confirm-del-dialog>
  </div>
</template>
<style lang="scss">
  .el-breadcrumb {
    margin-top: 50px;
    line-height: 22px;

    .el-breadcrumb__item__inner {
      font-family: PingFangSC-Light;
      font-size: 16px;
      font-weight: 300;
      color: #888888 !important;
    }
  }
  .ad-app-list {
    width: 100%;
    padding-right: 35px;
    font-size: 0px;

    a {
      font-family: PingFangSC-Regular;
      font-size: 13px;
      color: #4a90e2;

      &.mrg-l18 {
        margin-left: 18px;
      }
    }

    .addAd-btn {
      border-color: #F5A623;
      border: none;
      height: 40px;
      vertical-align: top;
      color: #fff;
      background: #F5A623;
      width: 108px;
      padding: 10px;
      line-height: 14px;
      font-size: 12px;
      transition: all 0.48s ease-out;

      &:hover {
        background: rgba(245, 166, 35, 0.7);
        border-color: rgba(245, 166, 35, 0.7);
      }

      span {
        font-family: PingFangSC-Semibold !important;
      }
      .add-logo {
        position: relative;
        right: 5px;
      }
    }

    .table-wrapper {
      width: 100%;
      margin-top: 30px;
      margin-bottom: 20px;

      .el-table {
        .el-table__body-wrapper {
          .not-default-space {
            .cell {
              font-size: 0px;
            }
          }
        }
        .el-table__row{
          td:first-child{
            .cell{
              white-space: nowrap;
            }
          }
        }
      }

      .cursor-point {
        .cell{
          cursor: pointer;
        }
      }
    }
  }
</style>
<script>
  import {mapState, mapActions} from 'vuex'
  import ConfirmDelDialog from '@/components/ConfirmDelDialog'
  export default {
    components: {
      ConfirmDelDialog
    },
    data () {
      return {
        dataLoading: false,
        appName: '',
        currentPage: 1,
        dialogDeleteVisible: false,
        detailOfDelApp: {}
      }
    },
    mounted () {
      this.searchApp()
    },
    computed: {
      ...mapState('app', [
        'tableData',
        'totalApps'
      ])
    },
    methods: {
      ...mapActions('app', [
        'applist',
        'delApp'
      ]),
      reviewDelApp (params) {
        this.detailOfDelApp = params
        this.$refs.delConfirmDialog.show()
      },
      handleDelete () {
        this.delApp(this.detailOfDelApp).then(() => {
          this.$refs.delConfirmDialog.hide()
        })
      },
      cellClick (row, column, cell, event) {
        if (column.property && ['num_keywords', 'top10_keywords'].indexOf(column.property) > -1) {
          let path = '/d/app/aso_keyword/' + row.appid
          if (column.property === 'top10_keywords') {
            path += '?isTop10=1'
          }
          this.$router.push(path)
        }
      },
      forwardTo (pathname, item) {
        let path = ''
        switch (pathname) {
          case 'app_add':
            path = '/d/app/add'
            break
          case 'app_rank':
            path = '/d/app/rank/' + item.appid
            break
          case 'app_keyword':
            path = '/d/app/aso_keyword/' + item.appid
            break
          case 'app_on':
            path = '/d/ad/new/' + item.appid + '?from=app_list'
            break
        }
        this.$router.push(path)
      },
      searchApp (page = 1) {
        this.currentPage = page
        this.dataLoading = true
        this.applist({
          keyword: this.appName,
          page: page
        }).then(() => {
          this.dataLoading = false
        })
      },
      handleCurrentChange (page) {
        this.searchApp(page)
      }
    }
  }
</script>



